import { Bookmark, Plus, Edit2, Trash2 } from 'lucide-react';
import { Player, Line } from '../types';
import { useLocalStorage } from '../hooks/useLocalStorage';

interface PresetsSectionProps {
  roster: Player[];
  setRoster: (roster: Player[]) => void;
  lines: Line[];
  setLines: (lines: Line[]) => void;
  jammerListLine: any[];
  setJammerListLine: (jammerListLine: any[]) => void;
}

interface PresetData {
  roster: Player[];
  lines: Line[];
  jammerListLine: any[];
}

export default function PresetsSection({ roster, setRoster, lines, setLines, jammerListLine, setJammerListLine }: PresetsSectionProps) {
  const [presets, setPresets] = useLocalStorage<Record<string, PresetData>>('rosterPresets', {});

  const createPreset = () => {
    const name = window.prompt('Nom du preset :');
    if (!name) return;
    if (presets[name] && !window.confirm('Ce preset existe dj. craser ?')) return;

    setPresets({
      ...presets,
      [name]: {
        roster: JSON.parse(JSON.stringify(roster)),
        lines: JSON.parse(JSON.stringify(lines)),
        jammerListLine: JSON.parse(JSON.stringify(jammerListLine)),
      },
    });
  };

  const loadPreset = (name: string) => {
    const preset = presets[name];
    if (!preset) return;
    if (!window.confirm(`Charger le preset  ${name}  ? Cela remplacera le roster et les lines actuels.`)) return;

    setRoster(JSON.parse(JSON.stringify(preset.roster)));
    setLines(JSON.parse(JSON.stringify(preset.lines)));
    setJammerListLine(JSON.parse(JSON.stringify(preset.jammerListLine || [])));
  };

  const renamePreset = (oldName: string) => {
    const newName = window.prompt('Nouveau nom :', oldName);
    if (!newName || newName === oldName) return;
    if (presets[newName] && !window.confirm('Un preset porte dj ce nom. craser ?')) return;

    const { [oldName]: removed, ...rest } = presets;
    setPresets({
      ...rest,
      [newName]: removed,
    });
  };

  const deletePreset = (name: string) => {
    if (!window.confirm(`Supprimer le preset  ${name}  ?`)) return;
    const { [name]: removed, ...rest } = presets;
    setPresets(rest);
  };

  return (
    <div className="mb-6">
      <h2 className="text-xl font-semibold mb-2 flex items-center gap-2">
        <Bookmark size={18} /> Presets
      </h2>
      <div className="flex flex-wrap gap-2">
        {Object.keys(presets).map((name) => (
          <div key={name} className="flex items-center bg-indigo-100 rounded-md overflow-hidden">
            <button
              onClick={() => loadPreset(name)}
              className="px-3 py-1 text-sm font-medium hover:bg-indigo-200"
            >
              {name}
            </button>
            <Edit2
              size={14}
              className="cursor-pointer mx-1 text-gray-600 hover:text-gray-800"
              onClick={() => renamePreset(name)}
            />
            <Trash2
              size={14}
              className="cursor-pointer mr-1 text-red-600 hover:text-red-800"
              onClick={() => deletePreset(name)}
            />
          </div>
        ))}

        <button
          onClick={createPreset}
          className="flex items-center gap-1 px-3 py-1 bg-green-200 hover:bg-green-300 rounded-md text-sm"
        >
          <Plus size={14} /> Nouveau preset
        </button>
      </div>
    </div>
  );
}
