import { Users, List, Play, BarChart2, Info, Book, Globe, BarChart } from 'lucide-react';
import { useTranslation } from 'react-i18next';

interface LayoutProps {
  children: React.ReactNode;
  currentPage: string;
  onPageChange: (page: string) => void;
}

const navItems = [
  { id: 'roster', icon: Users },
  { id: 'lines', icon: List },
  { id: 'game', icon: Play },
  { id: 'stats', icon: BarChart2 },
  { id: 'statsDetail', icon: BarChart },
  { id: 'documentation', icon: Book },
  { id: 'about', icon: Info },
];

export function Layout({ children, currentPage, onPageChange }: LayoutProps) {
  const { t, i18n } = useTranslation();

  const changeLanguage = () => {
    const newLang = i18n.language === 'fr' ? 'en' : 'fr';
    i18n.changeLanguage(newLang);
  };

  // Navigation mobile
  const MobileNav = () => (
    <nav className="mobile-nav bg-pink-600 text-white shadow-lg">
      <div className="flex items-center justify-around h-16">
        {navItems.map((item) => {
          const Icon = item.icon;
          return (
            <button
              key={item.id}
              onClick={() => onPageChange(item.id)}
              className={`flex flex-col items-center justify-center p-2 ${
                currentPage === item.id
                  ? 'text-white font-bold bg-pink-700 rounded-md shadow-lg transform scale-110'
                  : 'text-pink-200 hover:text-white hover:bg-pink-700 hover:bg-opacity-50 rounded-md transition-all duration-200'
              }`}
            >
              <Icon size={20} />
              <span className="text-xs mt-1">{t(`navigation.${item.id}`)}</span>
            </button>
          );
        })}
        <button
          onClick={() => changeLanguage()}
          className={`flex flex-col items-center justify-center p-2 ${
            i18n.language === 'fr' ? 'text-white' : 'text-pink-200'
          }`}
        >
          <Globe size={20} />
          <span className="text-xs mt-1">FR / EN</span>
        </button>
      </div>
    </nav>
  );

  // Navigation desktop
  const DesktopNav = () => (
    <header className="hidden bg-pink-600 text-white sticky top-0 z-50">
      <div className="max-w-7xl mx-auto px-4">
        <div className="h-14 flex items-center justify-between">
          <div className="flex-shrink-0 flex items-center">
            <div className="text-lg font-semibold whitespace-nowrap">
              RDLM <span className="text-sm">v0.4.1</span>
            </div>
          </div>

          <div className="flex items-center justify-center space-x-4">
            {navItems.map((item) => {
              const Icon = item.icon;
              return (
                <button
                  key={item.id}
                  onClick={() => onPageChange(item.id)}
                  className={`flex items-center space-x-2 px-3 py-2 rounded-md text-sm font-medium ${
                    currentPage === item.id
                      ? 'bg-pink-700'
                      : 'hover:bg-pink-500'
                  }`}
                >
                  <Icon size={18} />
                  <span>{t(`navigation.${item.id}`)}</span>
                </button>
              );
            })}
          </div>

          <button
            onClick={() => changeLanguage()}
            className={`w-16 h-8 flex items-center justify-center rounded-md text-sm font-medium hover:bg-pink-500 ${
              i18n.language === 'fr' ? 'bg-pink-700' : ''
            }`}
          >
            FR / EN
          </button>
        </div>
      </div>
    </header>
  );

  return (
    <div className="min-h-screen bg-gray-50 flex flex-col">
      <DesktopNav />
      
      <main className="flex-1 p-4 pb-20">
        {children}
      </main>

      <MobileNav />
    </div>
  );
}