import { useState, useRef } from 'react';
import { Player, Line, Stats, JammerLine } from '../types';
import { Plus, Trash2, Save, Edit2, Download, Upload, Star, X, Check, Circle } from 'lucide-react';
import PresetsSection from '../components/PresetsSection';

interface RosterPageProps {
  roster: Player[];
  setRoster: (roster: Player[]) => void;
  lines?: Line[];
  setLines?: (lines: Line[]) => void;
  jammerListLine?: JammerLine[];
  setJammerListLine?: (lines: JammerLine[]) => void;
  stats?: Stats;
  setStats?: (stats: Stats) => void;
  jamCount?: number;
  setJamCount?: (count: number) => void;
  inPenalty?: string[];
  setInPenalty?: (players: string[]) => void;
  currentPeriod?: number;
  onResetMatch?: () => void;
}

export function RosterPage({ 
  roster, 
  setRoster,
  lines = [],
  setLines = () => {},
  jammerListLine = [],
  setJammerListLine = () => {},
  stats = {},
  setStats = () => {},
  jamCount = 1,
  setJamCount = () => {},
  inPenalty = [],
  setInPenalty = () => {},
  currentPeriod = 0,
  onResetMatch = () => {},
}: RosterPageProps) {
  const [newPlayer, setNewPlayer] = useState({ name: '', number: '' });
  const [editingPlayer, setEditingPlayer] = useState<string | null>(null);
  const [editForm, setEditForm] = useState({ name: '', number: '' });
  const fileInputRef = useRef<HTMLInputElement>(null);
  const appStateInputRef = useRef<HTMLInputElement>(null);

  const addPlayer = () => {
    if (newPlayer.name && newPlayer.number) {
      setRoster([
        ...roster,
        {
          id: crypto.randomUUID(),
          name: newPlayer.name,
          number: newPlayer.number,
          isJammer: false,
          isPivot: false,
          isBlocker: false,
          isAvailable: true,
          onBreak: false,
          marker1: null,
          marker2: null,
          marker3: null,
        },
      ]);
      setNewPlayer({ name: '', number: '' });
    }
  };

  const startEditing = (player: Player) => {
    setEditingPlayer(player.id);
    setEditForm({ name: player.name, number: player.number });
  };

  const saveEdit = (playerId: string) => {
    if (editForm.name && editForm.number) {
      setRoster(roster.map(player =>
        player.id === playerId
          ? { ...player, name: editForm.name, number: editForm.number }
          : player
      ));
      setEditingPlayer(null);
    }
  };

  const toggleRole = (playerId: string, role: 'isJammer' | 'isPivot' | 'isBlocker') => {
    setRoster(
      roster.map((player) => {
        if (player.id === playerId) {
          const updatedPlayer = { ...player, [role]: !player[role] };
          
          if (role === 'isJammer' && player.isJammer && !updatedPlayer.isJammer) {
            setJammerListLine(jammerListLine.filter(jl => jl.jammer !== playerId));
          }
          
          return updatedPlayer;
        }
        return player;
      })
    );
  };

  const toggleJammerRotation = (playerId: string) => {
    const existingJammer = jammerListLine.find(jl => jl.jammer === playerId);
    
    const allPositions = jammerListLine.flatMap(jl => jl.positions || []);
    const maxPosition = allPositions.length > 0 ? Math.max(...allPositions) : 0;
    const nextPosition = maxPosition + 1;
    
    if (existingJammer) {
      setJammerListLine(jammerListLine.map(jl => 
        jl.jammer === playerId 
          ? { ...jl, positions: [...(jl.positions || []), nextPosition] }
          : jl
      ));
    } else {
      const player = roster.find(p => p.id === playerId);
      if (player?.isJammer) {
        setJammerListLine([
          ...jammerListLine,
          { 
            id: crypto.randomUUID(), 
            jammer: playerId,
            positions: [nextPosition]
          }
        ]);
      } else {
        alert('Ce joueur doit tre un jammer pour tre ajout  la rotation.');
      }
    }
  };

  const removeJammerPosition = (jammerId: string, position: number) => {
    const jammerLine = jammerListLine.find(jl => jl.jammer === jammerId);
    if (!jammerLine) return;

    // Rcuprer toutes les positions de tous les jammers et trouver la position maximale globale
    const allPositions = jammerListLine.flatMap(jl => jl.positions || []);
    const maxPosition = allPositions.length > 0 ? Math.max(...allPositions) : 0;
    
    // Vrifier si la position  supprimer est bien la position maximale globale
    if (position !== maxPosition) {
      alert('Vous ne pouvez supprimer que la dernire position de toute la rotation.');
      return;
    }

    const updatedList = jammerListLine.map(jl => {
      if (jl.jammer === jammerId) {
        const newPositions = jl.positions.filter(p => p !== position);
        return { ...jl, positions: newPositions };
      }
      return jl;
    });

    setJammerListLine(updatedList.filter(jl => jl.positions.length > 0));
  };

  const toggleAvailability = (playerId: string) => {
    setRoster(
      roster.map((player) =>
        player.id === playerId
          ? { ...player, isAvailable: !player.isAvailable }
          : player
      )
    );
  };

  const removePlayer = (playerId: string) => {
    if (jammerListLine.some(jl => jl.jammer === playerId)) {
      setJammerListLine(jammerListLine.filter(jl => jl.jammer !== playerId));
    }
    setRoster(roster.filter((player) => player.id !== playerId));
  };

  const toggleMarker = (playerId: string, markerNumber: 1 | 2 | 3) => {
    const player = roster.find(p => p.id === playerId);
    if (!player) return;
    
    // Cycle through colors: null -> green -> blue -> red -> null
    let newColor: 'green' | 'blue' | 'red' | null = null;
    const currentColor = player[`marker${markerNumber}`];
    
    if (currentColor === null || currentColor === undefined) {
      newColor = 'green';
    } else if (currentColor === 'green') {
      newColor = 'blue';
    } else if (currentColor === 'blue') {
      newColor = 'red';
    } else {
      newColor = null;
    }
    
    setRoster(roster.map(p => {
      if (p.id === playerId) {
        return { ...p, [`marker${markerNumber}`]: newColor };
      }
      return p;
    }));
  };

  const exportRoster = () => {
    const headers = ['Name,Number,Jammer,Pivot,Blocker,Available,InRotation,OnBreak,Marker1,Marker2,Marker3'];
    const csvContent = roster.map(player => 
      `${player.name},${player.number},${player.isJammer},${player.isPivot},${player.isBlocker},${player.isAvailable},${jammerListLine.some(jl => jl.jammer === player.id)},${player.onBreak},${player.marker1 || ''},${player.marker2 || ''},${player.marker3 || ''}`
    );
    
    const csvData = [...headers, ...csvContent].join('\n');
    const blob = new Blob([csvData], { type: 'text/csv' });
    const url = window.URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.setAttribute('download', 'roster.csv');
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    window.URL.revokeObjectURL(url);
  };

  const importRoster = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      const text = e.target?.result as string;
      if (!text) return;
      
      const lines = text.split('\n');
      
      const newRoster: Player[] = lines.slice(1)
        .filter(line => line.trim() !== '')
        .map(line => {
          const [name, number, isJammer, isPivot, isBlocker, available, inRotation, onBreak, marker1, marker2, marker3] = line.split(',');
          
          const player: Player = {
            id: crypto.randomUUID(),
            name,
            number,
            isJammer: isJammer === 'true',
            isPivot: isPivot === 'true',
            isBlocker: isBlocker === 'true',
            isAvailable: available === 'true',
            onBreak: onBreak === 'true',
            marker1: marker1 ? marker1 as 'green' | 'blue' | 'red' : null,
            marker2: marker2 ? marker2 as 'green' | 'blue' | 'red' : null,
            marker3: marker3 ? marker3 as 'green' | 'blue' | 'red' : null
          };
          
          if (inRotation === 'true' && isJammer === 'true') {
            const newJammerLine = { id: crypto.randomUUID(), jammer: player.id, positions: [1] };
            setJammerListLine([...jammerListLine, newJammerLine]);
          }
          
          return player;
        });

      setRoster(newRoster);
    };
    reader.readAsText(file);
    
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  const saveAppState = () => {
    // Rcuprer les statistiques dtailles du localStorage
    let detailedStats = null;
    try {
      const detailedStatsJson = window.localStorage.getItem('detailedStats');
      if (detailedStatsJson) {
        detailedStats = JSON.parse(detailedStatsJson);
      }
    } catch (error) {
      console.error('Error loading detailed stats from localStorage:', error);
    }
    
    const state = {
      roster,
      lines,
      jammerListLine,
      stats,
      jamCount,
      inPenalty,
      detailedStats
    };
    
    const blob = new Blob([JSON.stringify(state, null, 2)], { type: 'application/json' });
    const url = window.URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    const date = new Date().toISOString().split('T')[0];
    link.download = `derby-manager-state-${date}.json`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    window.URL.revokeObjectURL(url);
  };

  const loadAppState = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      try {
        const state = JSON.parse(e.target?.result as string);
        const updatedRoster = state.roster.map((player: Player) => ({
          ...player,
          isAvailable: true,
          onBreak: player.onBreak || false,
        }));
        
        const updatedJammerList = state.jammerListLine.map((jl: JammerLine) => ({
          ...jl,
          positions: jl.positions || [1]
        }));
        
        setRoster(updatedRoster);
        setLines(state.lines);
        setJammerListLine(updatedJammerList);
        setStats(state.stats);
        setJamCount(state.jamCount);
        setInPenalty(state.inPenalty);
        
        // Restaurer les statistiques dtailles dans le localStorage
        if (state.detailedStats) {
          try {
            window.localStorage.setItem('detailedStats', JSON.stringify(state.detailedStats));
            console.log('Detailed stats restored successfully');
          } catch (error) {
            console.error('Error restoring detailed stats:', error);
          }
        }
      } catch (error) {
        console.error('Error loading state:', error);
        alert('Error loading state file. Please make sure it\'s a valid Derby Manager state file.');
      }
    };
    reader.readAsText(file);
    
    if (appStateInputRef.current) {
      appStateInputRef.current.value = '';
    }
  };

  return (
    <div className="space-y-6">
      {/* Premire ligne de boutons */}
      <div className="flex justify-between mb-4">
        <div className="flex space-x-4">
          <button
            onClick={saveAppState}
            className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-green-600 hover:bg-green-700"
          >
            <Save size={18} className="mr-2" />
            Save
          </button>
          <label className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 cursor-pointer">
            <Upload size={18} className="mr-2" />
            Load
            <input
              type="file"
              accept=".json"
              onChange={loadAppState}
              className="hidden"
              ref={appStateInputRef}
            />
          </label>
          <label className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 cursor-pointer">
            <Upload size={18} className="mr-2" />
            Import Roster
            <input
              type="file"
              accept=".csv"
              onChange={importRoster}
              className="hidden"
              ref={fileInputRef}
            />
          </label>
          <button
            onClick={exportRoster}
            className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-green-600 hover:bg-green-700"
          >
            <Download size={18} className="mr-2" />
            Export Roster
          </button>
        </div>
        {currentPeriod !== 0 && (
          <button
            onClick={onResetMatch}
            className="px-4 py-2 rounded-md bg-red-600 text-white hover:bg-red-700"
          >
            Reset Match
          </button>
        )}
      </div>
      
      {/* Deuxime ligne - Presets */}
      <div className="mb-4">
        <PresetsSection 
          roster={roster} 
          setRoster={setRoster} 
          lines={lines} 
          setLines={setLines} 
          jammerListLine={jammerListLine}
          setJammerListLine={setJammerListLine}
        />
      </div>

      <div className="flex flex-col space-y-4">
        <h1 className="text-3xl font-bold">Team Roster</h1>
        
        <div className="bg-white rounded-lg shadow overflow-hidden">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-2 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  #
                </th>
                <th className="px-2 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Name
                </th>
                <th className="px-2 py-2 text-center text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Markers
                </th>
                <th className="px-2 py-2 text-center text-xs font-medium text-gray-500 uppercase tracking-wider">
                  J
                </th>
                <th className="px-2 py-2 text-center text-xs font-medium text-gray-500 uppercase tracking-wider">
                  P
                </th>
                <th className="px-2 py-2 text-center text-xs font-medium text-gray-500 uppercase tracking-wider">
                  B
                </th>
                <th className="hidden md:table-cell px-2 py-2 text-center text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Rotation
                </th>
                <th className="px-2 py-2 text-center text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Status
                </th>
                <th className="px-2 py-2 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  <span className="sr-only">Actions</span>
                </th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {roster.map((player, index) => {
                const jammerRotation = jammerListLine.find(jl => jl.jammer === player.id);
                
                return (
                  <tr key={player.id} className={!player.isAvailable ? 'bg-gray-50' : ''}>
                    <td className="px-2 py-2 whitespace-nowrap text-sm text-gray-500">
                      {index + 1}
                    </td>
                    <td className="px-2 py-2 whitespace-nowrap">
                      {editingPlayer === player.id ? (
                        <div className="flex flex-col space-y-1">
                          <input
                            type="text"
                            value={editForm.name}
                            onChange={(e) => setEditForm({ ...editForm, name: e.target.value })}
                            className="w-full border-gray-300 rounded-md shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
                          />
                          <input
                            type="text"
                            value={editForm.number}
                            onChange={(e) => setEditForm({ ...editForm, number: e.target.value })}
                            className="w-full border-gray-300 rounded-md shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
                          />
                        </div>
                      ) : (
                        <div className="text-sm font-medium text-gray-900">
                          {player.name} #{player.number}
                        </div>
                      )}
                    </td>
                    <td className="px-2 py-2 whitespace-nowrap text-center">
                      <div className="flex items-center justify-center space-x-1">
                        <button 
                          onClick={() => toggleMarker(player.id, 1)}
                          className="w-5 h-5 rounded-full focus:outline-none"
                          style={{ backgroundColor: player.marker1 || 'transparent', border: player.marker1 ? 'none' : '1px solid #ccc' }}
                          title={`Click to change marker 1 (${player.marker1 || 'none'})`}
                        />
                        <button 
                          onClick={() => toggleMarker(player.id, 2)}
                          className="w-5 h-5 rounded-full focus:outline-none"
                          style={{ backgroundColor: player.marker2 || 'transparent', border: player.marker2 ? 'none' : '1px solid #ccc' }}
                          title={`Click to change marker 2 (${player.marker2 || 'none'})`}
                        />
                        <button 
                          onClick={() => toggleMarker(player.id, 3)}
                          className="w-5 h-5 rounded-full focus:outline-none"
                          style={{ backgroundColor: player.marker3 || 'transparent', border: player.marker3 ? 'none' : '1px solid #ccc' }}
                          title={`Click to change marker 3 (${player.marker3 || 'none'})`}
                        />
                      </div>
                    </td>
                    <td className="px-2 py-2 text-center">
                      <button
                        onClick={() => toggleRole(player.id, 'isJammer')}
                        className={`w-6 h-6 rounded ${
                          player.isJammer ? 'bg-indigo-600' : 'bg-gray-200'
                        }`}
                      />
                    </td>
                    <td className="px-2 py-2 text-center">
                      <button
                        onClick={() => toggleRole(player.id, 'isPivot')}
                        className={`w-6 h-6 rounded ${
                          player.isPivot ? 'bg-indigo-600' : 'bg-gray-200'
                        }`}
                      />
                    </td>
                    <td className="px-2 py-2 text-center">
                      <button
                        onClick={() => toggleRole(player.id, 'isBlocker')}
                        className={`w-6 h-6 rounded ${
                          player.isBlocker ? 'bg-indigo-600' : 'bg-gray-200'
                        }`}
                      />
                    </td>
                    <td className="hidden md:table-cell px-2 py-2">
                      <div className="flex flex-col space-y-2">
                        <button
                          onClick={() => toggleJammerRotation(player.id)}
                          disabled={!player.isJammer}
                          className={`inline-flex items-center px-2 py-1 rounded-md text-xs w-24 justify-center ${
                            jammerRotation
                              ? 'bg-yellow-100 text-yellow-800'
                              : player.isJammer
                              ? 'bg-gray-100 text-gray-800 hover:bg-gray-200'
                              : 'bg-gray-50 text-gray-400 cursor-not-allowed'
                          }`}
                        >
                          <Star size={14} className="mr-1" />
                          {jammerRotation ? 'In Rotation' : 'Rotation'}
                        </button>

                        {jammerRotation && jammerRotation.positions && (
                          <div className="flex flex-wrap gap-1">
                            {jammerRotation.positions.sort((a, b) => a - b).map((pos) => (
                              <button
                                key={pos}
                                onClick={() => removeJammerPosition(player.id, pos)}
                                className={`inline-flex items-center px-1 py-0.5 rounded text-xs ${
                                  pos === Math.max(...jammerRotation.positions)
                                    ? 'bg-gray-100 text-gray-700 hover:bg-red-100 hover:text-red-700'
                                    : 'bg-gray-100 text-gray-400 cursor-not-allowed'
                                } transition-colors`}
                                disabled={pos !== Math.max(...jammerRotation.positions)}
                                title={pos !== Math.max(...jammerRotation.positions) ? 'Vous ne pouvez supprimer que la dernire position' : undefined}
                              >
                                {pos}
                              </button>
                            ))}
                          </div>
                        )}
                      </div>
                    </td>
                    <td className="px-2 py-2 text-center">
                      <div className="flex flex-col space-y-1">
                        <button
                          onClick={() => toggleAvailability(player.id)}
                          className={`inline-flex items-center justify-center px-2 py-1 rounded-md text-xs w-28 ${
                            !player.isAvailable
                              ? 'bg-red-100 text-red-800'
                              : 'bg-green-100 text-green-800 hover:bg-green-200'
                          }`}
                        >
                          {!player.isAvailable ? (
                            <X size={14} className="mr-1" />
                          ) : (
                            <Check size={14} className="mr-1" />
                          )}
                          {!player.isAvailable ? 'Unavailable' : 'Available'}
                        </button>
                      </div>
                    </td>
                    <td className="px-2 py-2 text-right">
                      <div className="flex flex-col space-y-1">
                        {editingPlayer === player.id ? (
                          <button
                            onClick={() => saveEdit(player.id)}
                            className="text-green-600 hover:text-green-900"
                          >
                            <Save size={16} />
                          </button>
                        ) : (
                          <button
                            onClick={() => startEditing(player)}
                            className="text-blue-600 hover:text-blue-900"
                          >
                            <Edit2 size={16} />
                          </button>
                        )}
                        <button
                          onClick={() => removePlayer(player.id)}
                          className="text-red-600 hover:text-red-900"
                        >
                          <Trash2 size={16} />
                        </button>
                      </div>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>

        <div className="flex items-center space-x-4">
          <input
            type="text"
            placeholder="Player Name"
            value={newPlayer.name}
            onChange={(e) => setNewPlayer({ ...newPlayer, name: e.target.value })}
            className="flex-1 rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
          />
          <input
            type="text"
            placeholder="Number"
            value={newPlayer.number}
            onChange={(e) => setNewPlayer({ ...newPlayer, number: e.target.value })}
            className="w-32 rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
          />
          <button
            onClick={addPlayer}
            className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700"
          >
            <Plus size={18} className="mr-2" />
            Add Player
          </button>
        </div>
      </div>
    </div>
  );
}