import React, { useRef, useState } from 'react';
import { Player, Stats, StatsByPeriod, PENALTY_TYPES } from '../types';
import { Download, Upload, Plus, Trash2 } from 'lucide-react';

// --- helpers -----------------
export const mergeStats = (s1: Stats, s2: Stats): Stats => {
  const total: Stats = { ...s1 };
  Object.entries(s2).forEach(([pid, ps]) => {
    const base = total[pid] || {
      penalties: 0,
      penaltyCount: 0,
      fouls: 0,
      breaks: 0,
      jamsPlayed: 0,
      penaltyTypes: {},
      starPasses: 0,
    };
    total[pid] = {
      penalties: base.penalties + ps.penalties,
      penaltyCount: base.penaltyCount + ps.penaltyCount,
      fouls: base.fouls + ps.fouls,
      breaks: base.breaks + ps.breaks,
      jamsPlayed: base.jamsPlayed + ps.jamsPlayed,
      penaltyTypes: { ...base.penaltyTypes, ...ps.penaltyTypes },
      starPasses: base.starPasses + ps.starPasses,
    };
  });
  return total;
};

interface StatsPageProps {
  roster: Player[];
  statsByPeriod: StatsByPeriod;
  setStats: (stats: Stats) => void;
  onAddPenalty?: (playerId: string, period: 'P1' | 'P2') => void;
  onRemovePenalty?: (playerId: string, period: 'P1' | 'P2') => void;
  currentPeriod: number;
  setCurrentPeriod: (p: number) => void;
}

export function StatsPage({ roster, statsByPeriod, setStats, onAddPenalty, onRemovePenalty, currentPeriod, setCurrentPeriod }: StatsPageProps) {
  const [view, setView] = useState<'P1' | 'P2' | 'TOTAL'>(currentPeriod === 2 ? 'P2' : 'P1');
  const fileInputRef = useRef<HTMLInputElement>(null);

  let effectiveStats: Stats = {};
  if (view === 'TOTAL') {
    effectiveStats = mergeStats(statsByPeriod.P1, statsByPeriod.P2);
  } else if (view === 'P1') {
    effectiveStats = statsByPeriod.P1;
  } else if (view === 'P2') {
    effectiveStats = statsByPeriod.P2;
  }

  const addPenalty = (playerId: string) => {
    if (view === 'TOTAL') return; // read-only
    if (onAddPenalty && (view === 'P1' || view === 'P2')) {
      onAddPenalty(playerId, view);
      return;
    }
    setStats((current: Stats) => {
      const playerStats = current[playerId] || {
        penalties: 0,
        penaltyCount: 0,
        fouls: 0,
        breaks: 0,
        jamsPlayed: 0,
        penaltyTypes: {},
        starPasses: 0
      };

      // Calcul du total P1+P2 (avant ajout)
      const p1 = statsByPeriod.P1 && statsByPeriod.P1[playerId] ? statsByPeriod.P1[playerId].penaltyCount || 0 : 0;
      const p2 = statsByPeriod.P2 && statsByPeriod.P2[playerId] ? statsByPeriod.P2[playerId].penaltyCount || 0 : 0;
      const pbTotalBefore = p1 + p2;
      if (pbTotalBefore >= 7) {
        // Dj expuls ou limite atteinte, on ne fait rien
        window.alert("Ce joueur a dj atteint 7 fautes au total et ne peut pas en recevoir davantage.");
        return current;
      }

      // Si on va atteindre 7, confirmation
      if (pbTotalBefore === 6) {
        const player = roster.find(p => p.id === playerId);
        const shouldContinue = window.confirm(
          ` ATTENTION \n\n${player?.name} (#${player?.number}) va atteindre 7 pnalits et sera expuls(e).\nVoulez-vous continuer ?`
        );
        if (!shouldContinue) {
          return current;
        }
      } else if (pbTotalBefore === 5) {
        const player = roster.find(p => p.id === playerId);
        window.alert(
          ` ATTENTION \n\n${player?.name} (#${player?.number}) a atteint 6 pnalits.\nUne 7me entranera une expulsion.`
        );
      }

      // Si on va dpasser 7, on bloque l'ajout
      if (pbTotalBefore + 1 > 7) {
        window.alert("Impossible d'ajouter plus de 7 fautes au total sur les deux priodes pour ce joueur.");
        return current;
      }

      // Ajout de la faute
      const newStats: Stats = {
        ...current,
        [playerId]: {
          ...playerStats,
          penalties: playerStats.penalties + 1,
          penaltyCount: playerStats.penaltyCount + 1,
        }
      };

      // Aprs ajout, si on atteint 7, expulsion
      if (pbTotalBefore + 1 >= 7) {
        const player = roster.find(p => p.id === playerId);
        const updatedRoster = roster.map(p => 
          p.id === playerId ? { ...p, isAvailable: false } : p
        );
        window.dispatchEvent(new CustomEvent('updateRoster', { detail: updatedRoster }));
        alert(` EXPULSION \n\n${player?.name} (#${player?.number}) a atteint 7 pnalits et est expuls(e) du match !`);
      }

      return newStats;
    });
  };

  const removePenalty = (playerId: string) => {
    if (view === 'TOTAL') return; // read-only
    if (onRemovePenalty && (view === 'P1' || view === 'P2')) {
      onRemovePenalty(playerId, view);
      return;
    }
    
    // Implementation fallback si onRemovePenalty n'est pas fourni
    setStats((current: Stats) => {
      const playerStats = current[playerId] || {
        penalties: 0,
        penaltyCount: 0,
        fouls: 0,
        breaks: 0,
        jamsPlayed: 0,
        penaltyTypes: {},
        starPasses: 0
      };
      
      if (playerStats.penaltyCount <= 0) {
        return current; // Rien  supprimer
      }
      
      // Supprimer une faute
      const newStats: Stats = {
        ...current,
        [playerId]: {
          ...playerStats,
          penalties: Math.max(playerStats.penalties - 1, 0),
          penaltyCount: Math.max(playerStats.penaltyCount - 1, 0),
        }
      };
      
      // Supprimer le dernier type de pnalit
      if (newStats[playerId].penaltyTypes) {
        const types = { ...newStats[playerId].penaltyTypes };
        delete types[newStats[playerId].penaltyCount]; // Supprimer la dernire pnalit
        newStats[playerId].penaltyTypes = types;
      }
      
      return newStats;
    });
  };

  const updatePenaltyType = (playerId: string, penaltyIndex: number, type: string) => {
    setStats((currentStats: Stats) => {
      const playerStats = currentStats[playerId] || { 
        penalties: 0, 
        penaltyCount: 0,
        fouls: 0, 
        breaks: 0, 
        jamsPlayed: 0,
        penaltyTypes: {},
        starPasses: 0
      };
      const updatedStats: Stats = {
        ...currentStats,
        [playerId]: {
          ...playerStats,
          penaltyTypes: {
            ...playerStats.penaltyTypes,
            [penaltyIndex]: type
          }
        }
      };
      return updatedStats;
    });
  };

  const exportStats = () => {
    const statsData = Object.entries(statsByPeriod).flatMap(([periodKey, periodStats]) =>
      Object.entries(periodStats).map(([playerId, playerStats]) => {
        const player = roster.find(p => p.id === playerId);
        return {
          period: periodKey,
          playerId,
          playerName: player?.name,
          playerNumber: player?.number,
          ...playerStats,
        };
      })
    );
    
    const blob = new Blob([JSON.stringify(statsData, null, 2)], { type: 'application/json' });
    const url = window.URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    const date = new Date().toISOString().split('T')[0];
    link.download = `derby-stats-${date}.json`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    window.URL.revokeObjectURL(url);
  };

  const importStats = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      try {
        const importedStats = JSON.parse(e.target?.result as string);
        const newStats: Stats = {};
        
        importedStats.forEach((stat: any) => {
          if (stat.playerId) {
            newStats[stat.playerId] = {
              penalties: stat.penalties || 0,
              penaltyCount: stat.penaltyCount || 0,
              fouls: stat.fouls || 0,
              breaks: stat.breaks || 0,
              jamsPlayed: stat.jamsPlayed || 0,
              penaltyTypes: stat.penaltyTypes || {},
              starPasses: stat.starPasses || 0
            };
          }
        });

        setStats(newStats);
      } catch (error) {
        console.error('Error importing stats:', error);
        alert('Error importing stats. Please make sure the file is valid.');
      }
    };
    reader.readAsText(file);
    
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  const renderPenaltyTypes = (playerId: string, playerStats: any) => {
    // Dtection si on est sur la vue TOTAL ou non
    const isTotal = view === 'TOTAL';
    // Dtection si on est sur mobile ou non (cran < 640px)
    const isMobile = window.matchMedia('(max-width: 640px)').matches;
    
    // Si on est en mode TOTAL, on n'affiche pas le dtail des pnalits
    if (isTotal) {
      return null; // Ne rien afficher dans la vue TOTAL
    }

    // Construire les options du slecteur
    const buildOptions = () => {
      return Object.entries(PENALTY_TYPES).map(([key, label]) => {
        // Version mobile : seulement la premire lettre du code
        // Version desktop : code complet avec description
        const displayText = isMobile ? key : `${key} - ${label}`;
        return (
          <option key={key} value={key}>
            {displayText}
          </option>
        );
      });
    };

    // Pour P1 et P2, on permet la modification des types de fautes
    const penaltySelectors = playerStats.penaltyCount > 0 
      ? Array.from({ length: playerStats.penaltyCount }, (_, index) => {
          const currentValue = playerStats.penaltyTypes?.[index] || '';
          
          return (
            <select
              key={index}
              value={currentValue}
              onChange={(e) => updatePenaltyType(playerId, index, e.target.value)}
              className="penalty-selector mb-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 text-sm p-2 min-w-[140px]"
            >
              <option value="">{isMobile ? "-" : "Type"}</option>
              {buildOptions()}
            </select>
          );
        })
      : null;

    return (
      <div className="mt-2">
        <div className="text-sm font-medium mb-1">Types:</div>
        <div className="grid grid-cols-2 gap-2">
          {penaltySelectors}
        </div>
      </div>
    );
  };

  // Fonction pour afficher les marqueurs d'un joueur
  const renderMarkers = (playerId: string) => {
    const player = roster.find(p => p.id === playerId);
    if (!player) return null;
    
    return (
      <div className="flex items-center space-x-1 ml-1">
        {player.marker1 && (
          <div className="w-3 h-3 rounded-full" style={{ backgroundColor: player.marker1 }} />
        )}
        {player.marker2 && (
          <div className="w-3 h-3 rounded-full" style={{ backgroundColor: player.marker2 }} />
        )}
        {player.marker3 && (
          <div className="w-3 h-3 rounded-full" style={{ backgroundColor: player.marker3 }} />
        )}
      </div>
    );
  };

  // totals
  let totals = { penalties: 0, penaltyCount: 0, fouls: 0, breaks: 0, jamsPlayed: 0, starPasses: 0 };
  if (view === 'TOTAL') {
    totals = Object.values(effectiveStats).reduce((acc, ps) => {
      acc.penalties += ps.penalties || 0;
      acc.penaltyCount += ps.penaltyCount || 0;
      acc.fouls += ps.fouls || 0;
      acc.breaks += ps.breaks || 0;
      acc.jamsPlayed += ps.jamsPlayed || 0;
      acc.starPasses += ps.starPasses || 0;
      return acc;
    }, { penalties: 0, penaltyCount: 0, fouls: 0, breaks: 0, jamsPlayed: 0, starPasses: 0 });
  } else {
    totals = Object.values(statsByPeriod[view]).reduce((acc, ps) => {
      acc.penalties += ps.penalties || 0;
      acc.penaltyCount += ps.penaltyCount || 0;
      acc.fouls += ps.fouls || 0;
      acc.breaks += ps.breaks || 0;
      acc.jamsPlayed += ps.jamsPlayed || 0;
      acc.starPasses += ps.starPasses || 0;
      return acc;
    }, { penalties: 0, penaltyCount: 0, fouls: 0, breaks: 0, jamsPlayed: 0, starPasses: 0 });
  }

  return (
    <div className="space-y-6">
      {/* Tabs */}
      <div className="flex space-x-2">
        {(['P1','P2','TOTAL'] as const).map(tab => (
          <button
            key={tab}
            onClick={() => {
              if(tab==='TOTAL') setView('TOTAL');
              else {
                setView(tab);
                setCurrentPeriod(tab==='P1'?1:2);
              }
            }}
            className={`px-4 py-2 rounded-md text-sm font-medium ${view===tab?'bg-indigo-600 text-white':'bg-gray-200 text-gray-700'}`}
          >
            {tab}
          </button>
        ))}
      </div>

      <h1 className="text-3xl font-bold">Statistics</h1>
      
      {/* Premire ligne de boutons */}
      <div className="flex mb-4">
        <div className="flex space-x-4 w-full">
          <label className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 cursor-pointer">
            <Upload size={18} className="mr-2" />
            Import Stats
            <input
              type="file"
              accept=".json"
              onChange={importStats}
              className="hidden"
              ref={fileInputRef}
            />
          </label>
          <button
            onClick={exportStats}
            className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700"
          >
            <Download size={18} className="mr-2" />
            Export Stats
          </button>
        </div>
      </div>
      
      {/* Deuxime ligne de boutons - preset */}
      <div className="flex mb-4">
        {/* Bouton preset  insrer ici - Le composant PresetsSection est habituellement dans la page RosterPage */}
      </div>

      <div className="bg-white rounded-lg shadow overflow-hidden">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                #
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Player
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Number
              </th>
              <th className="px-6 py-3 text-center text-xs font-medium text-gray-500 uppercase tracking-wider">
                Penalties
              </th>
              <th className="px-6 py-3 text-center text-xs font-medium text-gray-500 uppercase tracking-wider">
                Breaks
              </th>
              <th className="px-6 py-3 text-center text-xs font-medium text-gray-500 uppercase tracking-wider">
                Jams Played
              </th>
              <th className="px-6 py-3 text-center text-xs font-medium text-gray-500 uppercase tracking-wider">
                Star Passes
              </th>
              <th className="px-6 py-3 text-center text-xs font-medium text-gray-500 uppercase tracking-wider">
                Actions
              </th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {Object.entries(effectiveStats).map(([playerId, playerStats]) => {
              const player = roster.find(p => p.id === playerId);

              return (
                <tr key={playerId}>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                    {roster.indexOf(player) + 1}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="flex items-center">
                      <div className="flex items-center">
                        <span className="text-sm font-medium text-gray-900">{player.name}</span>
                        {renderMarkers(player.id)}
                      </div>
                      <div className="text-sm text-gray-500 ml-1">#{player.number}</div>
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">#{player.number}</td>
                  <td className="px-6 py-4">
                    <div className="text-center">
                      <span className={`text-lg font-bold ${
                        playerStats.penaltyCount >= 6 
                          ? 'text-red-600'
                          : playerStats.penaltyCount >= 5
                          ? 'text-orange-600'
                          : 'text-gray-900'
                      }`}>
                        {playerStats.penaltyCount}
                        {playerStats.penaltyCount >= 6 && ' '}
                      </span>
                      
                      {/* Version mobile simple sans slecteurs */}
                      <div className="md:hidden">
                        {playerStats.penaltyCount > 0 && (
                          <div className="text-xs mt-1 text-center">
                            {playerStats.penaltyCount} pnalits
                          </div>
                        )}
                      </div>
                      
                      {/* Version desktop avec slecteurs */}
                      <div className="hidden md:block">
                        {renderPenaltyTypes(playerId, playerStats)}
                      </div>
                    </div>
                  </td>
                  <td className="px-6 py-4 text-center text-lg">
                    {playerStats.breaks}
                  </td>
                  <td className="px-6 py-4 text-center text-lg">
                    {playerStats.jamsPlayed}
                  </td>
                  <td className="px-6 py-4 text-center text-lg">
                    {playerStats.starPasses || 0}
                  </td>
                  <td className="px-6 py-4 text-center">
                    {view!=='TOTAL' && (
                    <div className="flex space-x-2 justify-center">
                      <button
                        onClick={() => removePenalty(playerId)}
                        className="inline-flex items-center px-2 py-1 border border-transparent rounded shadow-sm text-xs font-medium text-white bg-gray-600 hover:bg-gray-700 disabled:opacity-50 disabled:cursor-not-allowed"
                        disabled={playerStats.penaltyCount <= 0}
                        title="Supprimer la dernire faute"
                      >
                        <Trash2 size={14} className="mr-1" />
                        -1
                      </button>
                      <button
                        onClick={() => addPenalty(playerId)}
                        className="inline-flex items-center px-2 py-1 border border-transparent rounded shadow-sm text-xs font-medium text-white bg-red-600 hover:bg-red-700"
                      >
                        <Plus size={14} className="mr-1" />
                        Add
                      </button>
                    </div>
                    )}
                  </td>
                </tr>
              );
            })}
          </tbody>
          <tfoot className="bg-gray-100">
            <tr>
              <td className="px-6 py-2 font-semibold" colSpan={3}>Total</td>
              <td className="px-6 py-2 text-center font-semibold">{totals.penaltyCount}</td>
              <td className="px-6 py-2 text-center font-semibold">{totals.breaks}</td>
              <td className="px-6 py-2 text-center font-semibold">{totals.jamsPlayed}</td>
              <td className="px-6 py-2 text-center font-semibold">{totals.starPasses}</td>
              <td></td>
            </tr>
          </tfoot>
        </table>
      </div>
    </div>
  );
} 