import { useState, useRef } from 'react';
import { Player, Line, JammerLine } from '../types';
import { Plus, Trash2, Copy, ArrowUp, ArrowDown, Download, Upload, Play, Pause, Save, Edit2 } from 'lucide-react';

interface LinesPageProps {
  roster: Player[];
  lines: Line[];
  setLines: (lines: Line[]) => void;
  jammerListLine: JammerLine[];
  setJammerListLine: (lines: JammerLine[]) => void;
  currentLineIndex: number;
  setCurrentLineIndex: (index: number) => void;
}

export function LinesPage({ 
  roster, 
  lines, 
  setLines,
  jammerListLine,
  setJammerListLine,
  currentLineIndex,
  setCurrentLineIndex
}: LinesPageProps) {
  const fileInputRef = useRef<HTMLInputElement>(null);
  const [editingName, setEditingName] = useState<string | null>(null);
  const [newLineName, setNewLineName] = useState('');

  const addLine = () => {
    const newLine = {
      id: crypto.randomUUID(),
      name: `Line ${lines.length + 1}`,
      isDormant: false,
      jammer: '',
      pivot: '',
      blocker1: '',
      blocker2: '',
      blocker3: '',
    };
    
    setLines([...lines, newLine]);
  };

  const copyLine = (lineToCopy: Line) => {
    const newLine = {
      ...lineToCopy,
      id: crypto.randomUUID(),
      name: `${lineToCopy.name} (Copy)`,
    };
    
    setLines([...lines, newLine]);
  };

  const updateLine = (lineId: string, position: keyof Line, playerId: string) => {
    setLines(
      lines.map((line) => {
        if (line.id === lineId) {
          const updatedLine = { ...line, [position]: playerId };
          
          // Si c'est un jammer et que la ligne n'est pas en mode Sleep,
          // l'ajouter  la rotation s'il n'y est pas dj
          if (position === 'jammer' && playerId && !line.isDormant) {
            const player = roster.find(p => p.id === playerId);
            const isInRotation = jammerListLine.some(jl => jl.jammer === playerId);
            
            if (player?.isJammer && !isInRotation) {
              const maxPosition = Math.max(...jammerListLine.flatMap(jl => jl.positions || []), 0);
              setJammerListLine([
                ...jammerListLine,
                { 
                  id: crypto.randomUUID(), 
                  jammer: playerId,
                  positions: [maxPosition + 1]
                }
              ]);
            }
          }
          
          return updatedLine;
        }
        return line;
      })
    );
  };

  const updateLineName = (lineId: string, name: string) => {
    setLines(
      lines.map((line) =>
        line.id === lineId ? { ...line, name } : line
      )
    );
    setEditingName(null);
    setNewLineName('');
  };

  const toggleSleep = (lineId: string) => {
    setLines(
      lines.map((line) => {
        if (line.id === lineId) {
          const newIsDormant = !line.isDormant;
          
          // Si la ligne devient dormante, retirer son jammer de la rotation
          if (newIsDormant && line.jammer) {
            setJammerListLine(jammerListLine.filter(jl => jl.jammer !== line.jammer));
          } else if (!newIsDormant && line.jammer) {
            // Si la ligne se rveille et a un jammer qui n'est pas dans la rotation
            const player = roster.find(p => p.id === line.jammer);
            const isInRotation = jammerListLine.some(jl => jl.jammer === line.jammer);
            
            if (player?.isJammer && !isInRotation) {
              const maxPosition = Math.max(...jammerListLine.flatMap(jl => jl.positions || []), 0);
              setJammerListLine([
                ...jammerListLine,
                { 
                  id: crypto.randomUUID(), 
                  jammer: line.jammer,
                  positions: [maxPosition + 1]
                }
              ]);
            }
          }
          
          return { ...line, isDormant: newIsDormant };
        }
        return line;
      })
    );
  };

  const removeLine = (lineId: string) => {
    const lineToRemove = lines.find(line => line.id === lineId);
    const indexToRemove = lines.findIndex(line => line.id === lineId);
    
    // Si la ligne a un jammer et que ce jammer n'apparat dans aucune autre ligne active
    if (lineToRemove?.jammer) {
      const jammerExistsInOtherLines = lines.some(line => 
        line.id !== lineId && !line.isDormant && line.jammer === lineToRemove.jammer
      );
      
      if (!jammerExistsInOtherLines) {
        setJammerListLine(jammerListLine.filter(jl => jl.jammer !== lineToRemove.jammer));
      }
    }
    
    if (indexToRemove === currentLineIndex) {
      setCurrentLineIndex(0);
    } else if (indexToRemove < currentLineIndex) {
      setCurrentLineIndex(currentLineIndex - 1);
    }
    
    setLines(lines.filter((line) => line.id !== lineId));
  };

  const moveLine = (index: number, direction: 'up' | 'down') => {
    const newLines = [...lines];
    const newIndex = direction === 'up' ? index - 1 : index + 1;
    
    if (newIndex >= 0 && newIndex < lines.length) {
      [newLines[index], newLines[newIndex]] = [newLines[newIndex], newLines[index]];
      setLines(newLines);
      
      if (index === currentLineIndex) {
        setCurrentLineIndex(newIndex);
      } else if (newIndex === currentLineIndex) {
        setCurrentLineIndex(index);
      }
    }
  };

  const exportLines = () => {
    const linesData = {
      lines: lines.map(line => ({
        ...line,
        jammerName: roster.find(p => p.id === line.jammer)?.name || '',
        pivotName: roster.find(p => p.id === line.pivot)?.name || '',
        blocker1Name: roster.find(p => p.id === line.blocker1)?.name || '',
        blocker2Name: roster.find(p => p.id === line.blocker2)?.name || '',
        blocker3Name: roster.find(p => p.id === line.blocker3)?.name || '',
      })),
      jammerListLine
    };
    
    const blob = new Blob([JSON.stringify(linesData, null, 2)], { type: 'application/json' });
    const url = window.URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    const date = new Date().toISOString().split('T')[0];
    link.download = `derby-lines-${date}.json`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    window.URL.revokeObjectURL(url);
  };

  const importLines = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      try {
        const importedData = JSON.parse(e.target?.result as string);
        const newLines: Line[] = importedData.lines.map((line: any) => ({
          id: crypto.randomUUID(),
          name: line.name || `Line ${lines.length + 1}`,
          isDormant: line.isDormant || false,
          jammer: line.jammer,
          pivot: line.pivot,
          blocker1: line.blocker1,
          blocker2: line.blocker2,
          blocker3: line.blocker3,
        }));
        
        setLines(newLines);
        setCurrentLineIndex(0);
      } catch (error) {
        console.error('Error importing lines:', error);
        alert('Error importing lines. Please make sure the file is valid.');
      }
    };
    reader.readAsText(file);
    
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  const isPlayerAvailable = (playerId: string): boolean => {
    const player = roster.find(p => p.id === playerId);
    return player?.isAvailable ?? false;
  };

  const sortedPlayers = {
    jammers: roster
      .filter((player) => player.isJammer)
      .sort((a, b) => {
        if (a.isAvailable === b.isAvailable) return a.name.localeCompare(b.name);
        return a.isAvailable ? -1 : 1;
      }),
    pivots: roster
      .filter((player) => player.isPivot)
      .sort((a, b) => {
        if (a.isAvailable === b.isAvailable) return a.name.localeCompare(b.name);
        return a.isAvailable ? -1 : 1;
      }),
    blockers: roster
      .filter((player) => player.isBlocker)
      .sort((a, b) => {
        if (a.isAvailable === b.isAvailable) return a.name.localeCompare(b.name);
        return a.isAvailable ? -1 : 1;
      }),
  };

  const renderMarkers = (playerId: string) => {
    const player = roster.find(p => p.id === playerId);
    if (!player) return null;
    
    return (
      <div className="flex items-center space-x-1 ml-1">
        {player.marker1 && (
          <div className="w-3 h-3 rounded-full" style={{ backgroundColor: player.marker1 }} />
        )}
        {player.marker2 && (
          <div className="w-3 h-3 rounded-full" style={{ backgroundColor: player.marker2 }} />
        )}
        {player.marker3 && (
          <div className="w-3 h-3 rounded-full" style={{ backgroundColor: player.marker3 }} />
        )}
      </div>
    );
  };

  const renderPlayerOption = (player: Player) => (
    <option 
      key={player.id} 
      value={player.id}
      className={`text-base ${!player.isAvailable ? 'text-red-500' : ''}`}
      disabled={!player.isAvailable}
    >
      {player.name} #{player.number} {!player.isAvailable ? '(Unavailable)' : ''}
    </option>
  );

  const renderPlayerSelect = (position: keyof Line, value: string, onChange: (e: React.ChangeEvent<HTMLSelectElement>) => void) => {
    const isInvalid = !value || (value && !isPlayerAvailable(value));
    
    return (
      <select
        value={value}
        onChange={onChange}
        className={`block w-full text-base rounded-md shadow-sm focus:border-indigo-500 focus:ring-indigo-500 ${
          isInvalid ? 'border-red-500 bg-red-50' : 'border-gray-300'
        }`}
      >
        <option value="">Select Player</option>
        {sortedPlayers[position === 'jammer' ? 'jammers' : position === 'pivot' ? 'pivots' : 'blockers'].map(renderPlayerOption)}
      </select>
    );
  };

  const renderJammerCount = (jammerId: string) => {
    const jammerLine = jammerListLine.find(jl => jl.jammer === jammerId);
    if (!jammerLine) return null;

    return (
      <div className="flex items-center space-x-1 text-sm text-gray-600 mt-1">
        <span>Positions: {jammerLine.positions.sort((a, b) => a - b).join(', ')}</span>
        {renderMarkers(jammerId)}
      </div>
    );
  };

  return (
    <div className="space-y-6">
      <div className="flex flex-col space-y-4">
        <h1 className="text-3xl font-bold">Lines</h1>
        
        <div className="flex space-x-4">
          <button
            onClick={exportLines}
            className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-green-600 hover:bg-green-700"
          >
            <Download size={18} className="mr-2" />
            Export Lines
          </button>
          <label className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 cursor-pointer">
            <Upload size={18} className="mr-2" />
            Import Lines
            <input
              type="file"
              accept=".json"
              onChange={importLines}
              className="hidden"
              ref={fileInputRef}
            />
          </label>
        </div>
      </div>

      <div className="bg-white rounded-lg shadow-lg p-6 mb-6">
        <h2 className="text-xl font-bold mb-4">Jammer Line List</h2>
        <div className="grid grid-cols-4 gap-4">
          {jammerListLine.map(jammerLine => {
            const player = roster.find(p => p.id === jammerLine.jammer);
            if (!player) return null;
            
            return (
              <div key={jammerLine.id} className="p-4 rounded-lg bg-white shadow-md">
                <div className="font-medium">
                  {player.name} #{player.number}
                </div>
                <div className="text-sm text-gray-600 mt-1">
                  Positions: {jammerLine.positions.sort((a, b) => a - b).join(', ')}
                </div>
                {renderMarkers(jammerLine.jammer)}
              </div>
            );
          })}
        </div>
      </div>

      <div className="bg-white rounded-lg shadow overflow-x-auto">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th className="w-12 px-2 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Line #
              </th>
              <th className="w-32 px-2 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Name
              </th>
              <th className="w-40 px-2 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Jammer
              </th>
              <th className="w-40 px-2 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Pivot
              </th>
              <th className="w-40 px-2 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Blocker 1
              </th>
              <th className="w-40 px-2 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Blocker 2
              </th>
              <th className="w-40 px-2 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Blocker 3
              </th>
              <th className="w-24 px-2 py-2 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                Actions
              </th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {lines.map((line, index) => (
              <tr 
                key={line.id}
                className={`${index === (currentLineIndex + 1) % lines.length ? 'bg-green-100' : ''} ${index === currentLineIndex ? 'bg-indigo-50 border-l-4 border-indigo-500' : line.isDormant ? 'bg-gray-50' : ''}`}
                onClick={() => setCurrentLineIndex(index)}
                style={{ cursor: 'pointer' }}
              >
                <td className="w-12 px-2 py-2 whitespace-nowrap text-sm font-medium text-gray-900">
                  {index + 1}
                  {index === currentLineIndex && (
                    <span className="ml-2 inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-indigo-100 text-indigo-800">
                      Current
                    </span>
                  )}
                  {line.isDormant && (
                    <span className="ml-2 inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-gray-100 text-gray-800">
                      Sleep
                    </span>
                  )}
                </td>
                <td className="w-32 px-2 py-2 whitespace-nowrap">
                  {editingName === line.id ? (
                    <div className="flex items-center space-x-2">
                      <input
                        type="text"
                        value={newLineName}
                        onChange={(e) => setNewLineName(e.target.value)}
                        className="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
                        placeholder="Enter line name"
                      />
                      <button
                        onClick={() => updateLineName(line.id, newLineName)}
                        className="text-green-600 hover:text-green-900"
                      >
                        <Save size={18} />
                      </button>
                    </div>
                  ) : (
                    <div className="flex items-center space-x-2">
                      <span>{line.name}</span>
                      <button
                        onClick={() => {
                          setEditingName(line.id);
                          setNewLineName(line.name);
                        }}
                        className="text-gray-400 hover:text-gray-600"
                      >
                        <Edit2 size={16} />
                      </button>
                    </div>
                  )}
                </td>
                <td className="w-40 px-2 py-2">
                  <div className="relative">
                    {renderPlayerSelect('jammer', line.jammer, (e) => updateLine(line.id, 'jammer', e.target.value))}
                    {line.jammer && renderMarkers(line.jammer)}
                  </div>
                </td>
                <td className="w-40 px-2 py-2">
                  <div className="relative">
                    {renderPlayerSelect('pivot', line.pivot, (e) => updateLine(line.id, 'pivot', e.target.value))}
                    {line.pivot && renderMarkers(line.pivot)}
                  </div>
                </td>
                <td className="w-40 px-2 py-2">
                  <div className="relative">
                    {renderPlayerSelect('blocker1', line.blocker1, (e) => updateLine(line.id, 'blocker1', e.target.value))}
                    {line.blocker1 && renderMarkers(line.blocker1)}
                  </div>
                </td>
                <td className="w-40 px-2 py-2">
                  <div className="relative">
                    {renderPlayerSelect('blocker2', line.blocker2, (e) => updateLine(line.id, 'blocker2', e.target.value))}
                    {line.blocker2 && renderMarkers(line.blocker2)}
                  </div>
                </td>
                <td className="w-40 px-2 py-2">
                  <div className="relative">
                    {renderPlayerSelect('blocker3', line.blocker3, (e) => updateLine(line.id, 'blocker3', e.target.value))}
                    {line.blocker3 && renderMarkers(line.blocker3)}
                  </div>
                </td>
                <td className="w-24 px-2 py-2 text-right space-x-2">
                  <button
                    onClick={() => moveLine(index, 'up')}
                    disabled={index === 0}
                    className={`text-gray-600 hover:text-gray-900 ${index === 0 ? 'opacity-50 cursor-not-allowed' : ''}`}
                    title="Move Up"
                  >
                    <ArrowUp size={18} />
                  </button>
                  <button
                    onClick={() => moveLine(index, 'down')}
                    disabled={index === lines.length - 1}
                    className={`text-gray-600 hover:text-gray-900 ${index === lines.length - 1 ? 'opacity-50 cursor-not-allowed' : ''}`}
                    title="Move Down"
                  >
                    <ArrowDown size={18} />
                  </button>
                  <button
                    onClick={() => copyLine(line)}
                    className="text-blue-600 hover:text-blue-900"
                    title="Copy Line"
                  >
                    <Copy size={18} />
                  </button>
                  <button
                    onClick={() => toggleSleep(line.id)}
                    className={`${
                      line.isDormant
                        ? 'text-green-600 hover:text-green-900'
                        : 'text-yellow-600 hover:text-yellow-900'
                    }`}
                    title={line.isDormant ? 'Wake Up Line' : 'Put Line to Sleep'}
                  >
                    {line.isDormant ? <Play size={18} /> : <Pause size={18} />}
                  </button>
                  <button
                    onClick={() => removeLine(line.id)}
                    className="text-red-600 hover:text-red-900"
                    title="Remove Line"
                  >
                    <Trash2 size={18} />
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      <button
        onClick={addLine}
        className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700"
      >
        <Plus size={18} className="mr-2" />
        Add Line
      </button>
    </div>
  );
}