import { useState, useEffect } from 'react';
import { Layout } from './components/Layout';
import { RosterPage } from './pages/RosterPage';
import { LinesPage } from './pages/LinesPage';
import { GamePage } from './pages/GamePage';
import { StatsPage } from './pages/StatsPage';
import { StatsDetailPage } from './pages/StatsDetailPage';
import { DocumentationPage } from './pages/DocumentationPage';
import { AboutPage } from './pages/AboutPage';
import { useLocalStorage } from './hooks/useLocalStorage';
import { Player, Line, Stats, JammerLine, StatsByPeriod } from './types';

export default function App() {
  const [hasVisited, setHasVisited] = useLocalStorage('hasVisited', false);
  const [currentPage, setCurrentPage] = useState(hasVisited ? 'roster' : 'about');
  const [roster, setRoster] = useLocalStorage<Player[]>('roster', []);
  const [lines, setLines] = useLocalStorage<Line[]>('lines', []);
  const [jammerListLine, setJammerListLine] = useLocalStorage<JammerLine[]>('jammerListLine', []);
  const [jamCount, setJamCount] = useLocalStorage('jamCount', 1);
  const [inPenalty, setInPenalty] = useLocalStorage<string[]>('inPenalty', []);
  const [currentLineIndex, setCurrentLineIndex] = useLocalStorage('currentLineIndex', 0);

  // --- Period & stats ---
  const [currentPeriod, setCurrentPeriod] = useLocalStorage<number>('currentPeriod', 0); // 0 = avant match

  // Migration : si ancien stockage 'stats' existe, dplacer vers P1
  const [legacyStats, setLegacyStats] = useLocalStorage<Stats>('stats', {});

  const emptyStatsByPeriod: StatsByPeriod = { P1: {}, P2: {} };
  const [statsByPeriod, setStatsByPeriod] = useLocalStorage<StatsByPeriod>('statsByPeriod', emptyStatsByPeriod);

  // Migration effect (run once)
  useEffect(() => {
    if (Object.keys(legacyStats).length > 0 && statsByPeriod.P1 && Object.keys(statsByPeriod.P1).length === 0) {
      setStatsByPeriod({ P1: legacyStats, P2: {} });
      setLegacyStats({});
    }
  }, []);

  // When we start Period 2, carry over PB counts and penaltyTypes only
  useEffect(() => {
    if (currentPeriod === 2 && Object.keys(statsByPeriod.P2 || {}).length === 0) {
      setStatsByPeriod(prev => {
        const p1 = prev.P1 || {};
        const newP2: Stats = {};
        Object.entries(p1).forEach(([pid, stats]) => {
          newP2[pid] = {
            penalties: 0,
            penaltyCount: 0, // P2 dmarre  zro
            fouls: 0,
            breaks: 0,
            jamsPlayed: 0,
            penaltyTypes: {}, // pas de report
            starPasses: 0,
          };
        });
        return { ...prev, P2: newP2 };
      });
    }
  }, [currentPeriod]);

  const periodKey: keyof StatsByPeriod = currentPeriod === 2 ? 'P2' : 'P1'; // pre-match (0) defaults to P1

  const stats: Stats = (statsByPeriod[periodKey] || {}) as Stats;

  type StatsSetter = Stats | ((prev: Stats) => Stats);

  // setStats synchronis avec l'tat global pour viter les dcalages entre priodes
  const setStats = (update: StatsSetter) => {
    setStatsByPeriod(prev => {
      // Toujours recalculer le total  partir du dernier tat global
      const prevStats = (prev[periodKey] || {}) as Stats;
      const newStats = typeof update === 'function' ? (update as (p: Stats) => Stats)({ ...prevStats }) : update;
      // On met  jour la priode courante
      const next = { ...prev, [periodKey]: newStats };
      return next;
    });
  };

  // Global reset before match
  const resetMatch = () => {
    setCurrentPeriod(0);
    setStatsByPeriod({ P1: {}, P2: {} });
    setJamCount(1);
    setInPenalty([]);
    setCurrentLineIndex(0);
    // also clear roster breaks
    setRoster(prev => prev.map(p => ({ ...p, onBreak: false, isAvailable: true })));
  };

  useEffect(() => {
    if (!hasVisited) {
      setHasVisited(true);
    }
  }, [hasVisited, setHasVisited]);

  useEffect(() => {
    const handleRosterUpdate = (event: CustomEvent<Player[]>) => {
      setRoster(event.detail);
    };

    window.addEventListener('updateRoster', handleRosterUpdate as EventListener);

    return () => {
      window.removeEventListener('updateRoster', handleRosterUpdate as EventListener);
    };
  }, [setRoster]);

  useEffect(() => {
    document.addEventListener('touchmove', (e) => {
      if (e.target === document.body) {
        e.preventDefault();
      }
    }, { passive: false });

    let lastTouchEnd = 0;
    document.addEventListener('touchend', (e) => {
      const now = Date.now();
      if (now - lastTouchEnd <= 300) {
        e.preventDefault();
      }
      lastTouchEnd = now;
    }, { passive: false });
  }, []);

  // Central add penalty handler based on total P1+P2
  const handleAddPenalty = (playerId: string, period: 'P1' | 'P2') => {
    const p1 = statsByPeriod.P1[playerId]?.penaltyCount || 0;
    const p2 = statsByPeriod.P2[playerId]?.penaltyCount || 0;
    const total = p1 + p2;
    if (total >= 7) {
      window.alert("Ce joueur a dj 7 fautes au total et ne peut pas en recevoir davantage.");
      return;
    }
    if (total === 6) {
      const player = roster.find(p => p.id === playerId);
      const ok = window.confirm(
        ` ATTENTION \n\n${player?.name} (#${player?.number}) va atteindre 7 pnalits et sera expuls(e).\nContinuer ?`
      );
      if (!ok) return;
    } else if (total === 5) {
      const player = roster.find(p => p.id === playerId);
      window.alert(
        ` ATTENTION \n\n${player?.name} (#${player?.number}) a atteint 6 pnalits.\nUne 7e entranera expulsion.`
      );
    }
    // Update stats for specified period
    const key = period;
    setStatsByPeriod(prev => {
      const periodStats = { ...(prev[key] || {}) };
      const stats = periodStats[playerId] || { penalties:0, penaltyCount:0, fouls:0, breaks:0, jamsPlayed:0, penaltyTypes:{}, starPasses:0 };
      const updated = { ...stats, penalties: stats.penalties + 1, penaltyCount: stats.penaltyCount + 1 };
      periodStats[playerId] = updated;
      return { ...prev, [key]: periodStats };
    });
    // Expulsion
    if (total + 1 >= 7) {
      const player = roster.find(p => p.id === playerId);
      const updatedRoster = roster.map(p => p.id === playerId ? { ...p, isAvailable: false } : p);
      window.dispatchEvent(new CustomEvent('updateRoster', { detail: updatedRoster }));
      window.alert(` EXPULSION \n\n${player?.name} (#${player?.number}) expuls(e).`);
    }
  };

  // Fonction pour supprimer une faute (version ngative de handleAddPenalty)
  const handleRemovePenalty = (playerId: string, period: 'P1' | 'P2') => {
    // Vrifier si le joueur a des fautes dans cette priode
    const playerStats = statsByPeriod[period]?.[playerId];
    if (!playerStats || playerStats.penaltyCount <= 0) {
      window.alert("Ce joueur n'a pas de faute  supprimer dans cette priode.");
      return;
    }

    // Mise  jour des stats pour la priode spcifie
    setStatsByPeriod(prev => {
      const periodStats = { ...(prev[period] || {}) };
      const stats = periodStats[playerId] || { penalties:0, penaltyCount:0, fouls:0, breaks:0, jamsPlayed:0, penaltyTypes:{}, starPasses:0 };
      
      // Supprimer la dernire faute
      const updated = { 
        ...stats, 
        penalties: Math.max(stats.penalties - 1, 0), 
        penaltyCount: Math.max(stats.penaltyCount - 1, 0) 
      };
      
      // Supprimer le dernier type de pnalit
      if (updated.penaltyTypes) {
        const types = { ...updated.penaltyTypes };
        delete types[updated.penaltyCount]; // Supprimer la dernire pnalit
        updated.penaltyTypes = types;
      }
      
      periodStats[playerId] = updated;
      return { ...prev, [period]: periodStats };
    });
  };

  const renderPage = () => {
    switch (currentPage) {
      case 'roster':
        return (
          <RosterPage 
            roster={roster} 
            setRoster={setRoster}
            lines={lines}
            setLines={setLines}
            jammerListLine={jammerListLine}
            setJammerListLine={setJammerListLine}
            stats={stats}
            setStats={setStats}
            jamCount={jamCount}
            setJamCount={setJamCount}
            inPenalty={inPenalty}
            setInPenalty={setInPenalty}
            currentPeriod={currentPeriod}
            onResetMatch={resetMatch}
          />
        );
      case 'lines':
        return (
          <LinesPage 
            roster={roster} 
            lines={lines} 
            setLines={setLines}
            jammerListLine={jammerListLine}
            setJammerListLine={setJammerListLine}
            currentLineIndex={currentLineIndex}
            setCurrentLineIndex={setCurrentLineIndex}
          />
        );
      case 'game':
        return (
          <GamePage
            roster={roster}
            lines={lines}
            jammerListLine={jammerListLine}
            stats={stats}
            statsByPeriod={statsByPeriod}
            setStats={setStats}
            jamCount={jamCount}
            setJamCount={setJamCount}
            inPenalty={inPenalty}
            setInPenalty={setInPenalty}
            currentLineIndex={currentLineIndex}
            setCurrentLineIndex={setCurrentLineIndex}
            currentPeriod={currentPeriod}
            setCurrentPeriod={setCurrentPeriod as any}
            onResetMatch={resetMatch}
          />
        );
      case 'stats':
        return (
          <StatsPage
            roster={roster}
            statsByPeriod={statsByPeriod}
            setStats={setStats}
            currentPeriod={currentPeriod}
            setCurrentPeriod={setCurrentPeriod}
            onAddPenalty={handleAddPenalty}
            onRemovePenalty={handleRemovePenalty}
          />
        );
      case 'statsDetail':
        return (
          <StatsDetailPage
            roster={roster}
            statsByPeriod={statsByPeriod}
            setStats={setStats}
            currentPeriod={currentPeriod}
            setCurrentPeriod={setCurrentPeriod}
          />
        );
      case 'documentation':
        return <DocumentationPage />;
      case 'about':
        return <AboutPage />;
      default:
        return <RosterPage roster={roster} setRoster={setRoster} />;
    }
  };

  return (
    <Layout currentPage={currentPage} onPageChange={setCurrentPage}>
      {renderPage()}
    </Layout>
  );
}