export interface Player {
  id: string;
  name: string;
  number: string;
  isJammer: boolean;
  isPivot: boolean;
  isBlocker: boolean;
  isAvailable: boolean;
  onBreak: boolean;
  marker1?: 'green' | 'blue' | 'red' | null;
  marker2?: 'green' | 'blue' | 'red' | null;
  marker3?: 'green' | 'blue' | 'red' | null;
}

export interface Line {
  id: string;
  name: string;
  isDormant: boolean;
  jammer: string;
  pivot: string;
  blocker1: string;
  blocker2: string;
  blocker3: string;
}

export interface JammerLine {
  id: string;
  jammer: string;
  positions: number[];
}

export interface PlayerStats {
  penalties: number;
  penaltyCount: number;
  fouls: number;
  breaks: number;
  jamsPlayed: number;
  penaltyTypes: Record<string, string>;
  starPasses: number;
}

export type Stats = Record<string, PlayerStats>;

// --- Priodes de jeu ---
export type Period = 1 | 2;

export interface StatsByPeriod {
  P1: Stats;
  P2: Stats;
}

export interface NavItem {
  id: string;
  label: string;
  icon: string;
}

export interface AppState {
  roster: Player[];
  lines: Line[];
  jammerListLine: JammerLine[];
  stats: Stats;
  statsByPeriod: StatsByPeriod;
  jamCount: number;
  inPenalty: string[];
}

// Roller Derby standard penalty codes
export const PENALTY_TYPES: Record<string, string> = {
  "BB": "Back Block",
  "H": "High Block",
  "L": "Low Block",
  "F": "Forearm",
  "C": "Cut Track",
  "MP": "Multiplayer",
  "IB": "Illegal Block",
  "OOB": "Out of Bounds",
  "I": "Illegal Procedure",
  "SB": "Stop Block",
};

export type PenaltyType = keyof typeof PENALTY_TYPES;