import { useState, useEffect } from 'react';
import { Player, Line, Stats, JammerLine, PENALTY_TYPES, StatsByPeriod } from '../types';
import { Plus, Trash2, RotateCcw } from 'lucide-react';
import { useLocalStorage } from '../hooks/useLocalStorage';

interface GamePageProps {
  roster: Player[];
  lines: Line[];
  jammerListLine: JammerLine[];
  stats: Stats;
  setStats: (stats: Stats) => void;
  jamCount: number;
  setJamCount: (count: number) => void;
  inPenalty: string[];
  setInPenalty: (players: string[]) => void;
  currentLineIndex: number;
  setCurrentLineIndex: (index: number) => void;
  currentPeriod: number;
  setCurrentPeriod: (p: number) => void;
}

type GamePagePropsWithReset = GamePageProps & { onResetMatch?: () => void; statsByPeriod?: StatsByPeriod };

export default function GamePage({
  roster,
  lines,
  jammerListLine,
  stats,
  setStats,
  jamCount,
  setJamCount,
  inPenalty,
  setInPenalty,
  currentLineIndex,
  setCurrentLineIndex,
  currentPeriod,
  setCurrentPeriod,
  onResetMatch,
  statsByPeriod
}: GamePagePropsWithReset) {
  const activeLines = lines.filter(line => !line.isDormant);

  const [currentLineOverride, setCurrentLineOverride] = useLocalStorage<Partial<Line>>('currentLineOverride', {
    jammer: activeLines[currentLineIndex]?.jammer || '',
    pivot: activeLines[currentLineIndex]?.pivot || ''
  });
  const [nextLineOverride, setNextLineOverride] = useLocalStorage<Partial<Line>>('nextLineOverride', {
    jammer: activeLines[(currentLineIndex + 1) % activeLines.length]?.jammer || '',
    pivot: activeLines[(currentLineIndex + 1) % activeLines.length]?.pivot || ''
  });
  const [breakPlayers, setBreakPlayers] = useLocalStorage<string[]>('breakPlayers', []);
  const [autoResetEnabled, setAutoResetEnabled] = useLocalStorage('autoResetEnabled', false);
  const [jammerWaitForPB, setJammerWaitForPB] = useLocalStorage('jammerWaitForPB', true);
  const [usedTTOs, setUsedTTOs] = useLocalStorage<boolean[]>('usedTTOs', [false, false, false]);
  const [usedORs, setUsedORs] = useLocalStorage<boolean[]>('usedORs', [false, false]);
  const [starPassDone, setStarPassDone] = useState(false);
  // State pour forcer la mise  jour de la rotation des jammers
  const [forceRotationUpdate, setForceRotationUpdate] = useState(false);

  useEffect(() => {
    setStarPassDone(false);
  }, [jamCount]);

  const baseCurrentLine = activeLines[currentLineIndex] || {
    id: '',
    name: '',
    isDormant: false,
    jammer: activeLines[0]?.jammer || 'defaultJammer',
    pivot: activeLines[0]?.pivot || 'defaultPivot',
    blocker1: '',
    blocker2: '',
    blocker3: '',
  };
  
  const baseNextLine = activeLines[(currentLineIndex + 1) % activeLines.length] || {
    id: '',
    name: '',
    isDormant: false,
    jammer: activeLines[1]?.jammer || 'defaultNextJammer',
    pivot: activeLines[1]?.pivot || 'defaultNextPivot',
    blocker1: '',
    blocker2: '',
    blocker3: '',
  };

  const currentLine = {
    ...baseCurrentLine,
    ...currentLineOverride
  };

  const nextLine = {
    ...baseNextLine,
    ...nextLineOverride
  };

  const currentJammerInPenalty = currentLine.jammer && inPenalty.includes(currentLine.jammer);

  const toggleTTO = (index: number) => {
    setUsedTTOs(current => 
      current.map((value, i) => i === index ? !value : value)
    );
  };

  const toggleOR = (index: number) => {
    setUsedORs(current => 
      current.map((value, i) => i === index ? !value : value)
    );
  };

  const getNextJammerInRotation = (currentJammerId: string): string => {
    // Si aucune rotation dfinie, retourner une chane vide
    if (jammerListLine.length === 0) return '';
    
    // Crer un tableau ordonn des jammers par position
    const jammersInOrder = jammerListLine
      .map(jl => ({ jammerId: jl.jammer, position: Math.max(...jl.positions) }))
      .sort((a, b) => a.position - b.position)
      .map(j => j.jammerId);
    
    // Si aucun jammer dans la rotation, retourner une chane vide
    if (jammersInOrder.length === 0) return '';
    
    // Trouver l'index du jammer actuel
    const currentIndex = jammersInOrder.indexOf(currentJammerId);
    
    // Si le jammer n'est pas dans la rotation, retourner le premier jammer
    if (currentIndex === -1) return jammersInOrder[0];
    
    // Sinon, retourner le jammer suivant dans la rotation
    const nextIndex = (currentIndex + 1) % jammersInOrder.length;
    return jammersInOrder[nextIndex];
  };

  const nextJam = () => {
    // Mettre  jour l'index de la ligne actuelle
    const newIndex = (currentLineIndex + 1) % activeLines.length;
    setCurrentLineIndex(newIndex);
    
    // Prparer la nouvelle ligne courante
    const newCurrentLine: Partial<Line> = {};
    
    // 1. GESTION DES JOUEURS EN PENALTY BOX
    // Garder tous les joueurs qui sont en penalty box dans la nouvelle ligne
    Object.entries(currentLine).forEach(([position, playerId]) => {
      if (playerId && inPenalty.includes(playerId)) {
        newCurrentLine[position as keyof Line] = playerId;
      }
    });
    
    // 2. GESTION DU JAMMER
    // Si le jammer actuel est en PB, il reste en place quel que soit le mode
    if (currentJammerInPenalty) {
      newCurrentLine.jammer = currentLine.jammer;
    } else {
      // Sinon, prendre le jammer de la next line
      newCurrentLine.jammer = nextLine.jammer;
    }
    
    // 3. GESTION DU PIVOT ET DES BLOCKERS
    if (!newCurrentLine.pivot) {
      const nextPivot = nextLine.pivot;
      if (nextPivot && !breakPlayers.includes(nextPivot) && !Object.values(newCurrentLine).includes(nextPivot)) {
        newCurrentLine.pivot = nextPivot;
      }
    }
    
    const blockerPositions = ['blocker1', 'blocker2', 'blocker3'] as const;
    
    const availableBlockers = blockerPositions
      .map(pos => nextLine[pos])
      .filter(id => id && !breakPlayers.includes(id) && !Object.values(newCurrentLine).includes(id)) as string[];
    
    const emptyBlockerPositions = blockerPositions.filter(pos => !newCurrentLine[pos]);
    
    emptyBlockerPositions.forEach((position, index) => {
      if (availableBlockers[index]) {
        newCurrentLine[position] = availableBlockers[index];
      }
    });
    
    // 4. MISE  JOUR DES STATS
    const playersInNewLine = Object.values(newCurrentLine).filter(Boolean);
    setStats((currentStats: Stats) => {
      const newStats: Stats = { ...currentStats };
      
      // Mettre  jour jams played pour tous les joueurs dans la ligne
      playersInNewLine.forEach(playerId => {
        if (playerId) {
          const playerStats = newStats[playerId] || {
            penalties: 0,
            penaltyCount: 0,
            fouls: 0,
            breaks: 0,
            jamsPlayed: 0,
            penaltyTypes: {},
            starPasses: 0
          };
          
          newStats[playerId] = {
            ...playerStats,
            jamsPlayed: playerStats.jamsPlayed + 1,
          };
        }
      });
      
      // Mettre  jour breaks pour les joueurs en pause
      Object.values(currentLine).forEach(playerId => {
        if (playerId) {
          const player = roster.find(p => p.id === playerId);
          
          if (player?.onBreak) {
            const playerStats = newStats[playerId] || {
              penalties: 0,
              penaltyCount: 0,
              fouls: 0,
              breaks: 0,
              jamsPlayed: 0,
              penaltyTypes: {},
              starPasses: 0
            };
            
            newStats[playerId] = {
              ...playerStats,
              breaks: playerStats.breaks + 1,
            };
          }
        }
      });
      
      return newStats as Stats;
    });
    
    // 5. MISE  JOUR DE LA LINE ACTUELLE
    setCurrentLineOverride(newCurrentLine);
    
    // 6. MISE  JOUR DE LA NEXT LINE POUR LE PROCHAIN JAM
    if (autoResetEnabled) {
      setNextLineOverride({});
    } else {
      const computeNextJammer = () => {
        // Si aucune rotation : on garde la valeur existante
        if (jammerListLine.length === 0) return nextLine.jammer;

        // Cas o le jammer actuel est en PB
        if (currentJammerInPenalty) {
          if (jammerWaitForPB) {
            // On fige la rotation : Next reste inchang
            return nextLine.jammer;
          }
          // don't wait PB : on avance par rapport au jammer qui TAIT en Next avant le clic
          return getNextJammerInRotation(nextLine.jammer || '');
        }

        // Jammer pas en PB : rotation normale par rapport au nouveau current jammer
        return getNextJammerInRotation(newCurrentLine.jammer || '');
      };

      const nextJammer = computeNextJammer();
      setNextLineOverride({ jammer: nextJammer });
    }
    
    // Rinitialiser les breaks 
    setBreakPlayers([]);
    // Incrmentation du jam count
    setJamCount(jamCount + 1);
    // Rinitialiser le Star Pass (autoriser un nouveau)
    setStarPassDone(false);
    
    // Rinitialiser le flag de rotation
    if (forceRotationUpdate) {
      setForceRotationUpdate(false);
    }
  };

  const resetNextLine = () => {
    setNextLineOverride({});
  };

  const toggleAutoReset = () => {
    setAutoResetEnabled(!autoResetEnabled);
  };

  const copyLineToNext = (sourceLine: Line) => {
    setNextLineOverride({
      jammer: sourceLine.jammer,
      pivot: sourceLine.pivot,
      blocker1: sourceLine.blocker1,
      blocker2: sourceLine.blocker2,
      blocker3: sourceLine.blocker3,
    });
  };

  const resetGame = () => {
    if (window.confirm('Are you sure you want to reset the period? This will clear all stats while keeping the jam count, TTOs, and ORs.')) {
      setStats({});
      setInPenalty([]);
      setBreakPlayers([]);
      setCurrentLineIndex(0);
      setCurrentLineOverride({});
      setNextLineOverride({});
    }
  };

  const allPlayers = {
    jammers: roster.filter((player) => player.isJammer && player.isAvailable && !player.onBreak),
    pivots: roster.filter((player) => player.isPivot && player.isAvailable && !player.onBreak),
    blockers: roster.filter((player) => player.isBlocker && player.isAvailable && !player.onBreak),
  };

  const sortedPlayers = {
    jammers: allPlayers.jammers.sort((a, b) => a.name.localeCompare(b.name)),
    pivots: allPlayers.pivots.sort((a, b) => a.name.localeCompare(b.name)),
    blockers: allPlayers.blockers.sort((a, b) => a.name.localeCompare(b.name)),
  };

  const isPlayerInLine = (playerId: string, line: Partial<Line>): boolean => {
    return Object.values(line).includes(playerId);
  };

  const updatePenaltyType = (playerId: string, type: string) => {
    setStats((currentStats: Stats) => {
      const playerStats = currentStats[playerId] || {
        penalties: 0,
        penaltyCount: 0,
        fouls: 0,
        breaks: 0,
        jamsPlayed: 0,
        penaltyTypes: {},
        starPasses: 0
      };

      const newStats: Stats = {
        ...currentStats,
        [playerId]: {
          ...playerStats,
          penaltyTypes: {
            ...playerStats.penaltyTypes,
            [playerStats.penaltyCount - 1]: type
          }
        }
      };

      return newStats as Stats;
    });
  };

  const togglePenalty = (playerId: string) => {
    const isEnteringPenalty = !inPenalty.includes(playerId);
    const player = roster.find(p => p.id === playerId);
    // Obtenir les statistiques de la priode en cours
    // Calculer le total des fautes sur les deux priodes
    const p1 = statsByPeriod?.P1 && statsByPeriod.P1[playerId] ? statsByPeriod.P1[playerId].penaltyCount || 0 : 0;
    const p2 = statsByPeriod?.P2 && statsByPeriod.P2[playerId] ? statsByPeriod.P2[playerId].penaltyCount || 0 : 0;
    const totalPenalties = p1 + p2;
    
    // Si on entre en pnalit et qu'on a dj atteint 7 fautes au total, bloquer
    if (isEnteringPenalty && totalPenalties >= 7) {
      window.alert("Ce joueur a dj atteint 7 fautes au total et ne peut pas en recevoir davantage.");
      return;
    }

    // Alerte  6 fautes au total
    if (isEnteringPenalty && totalPenalties === 6) {
      const shouldContinue = window.confirm(
        ` ATTENTION \n\n${player?.name} (#${player?.number}) va atteindre 7 pnalits au total (${p1} en P1 + ${p2} en P2 + 1).\nCela entranera une expulsion.\n\nVoulez-vous continuer ?`
      );
      if (!shouldContinue) {
        return;
      }
    }

    setInPenalty((current) => {
      const newPenalties = isEnteringPenalty
        ? [...current, playerId]
        : current.filter((id) => id !== playerId);
      return newPenalties;
    });

    setStats((currentStats: Stats) => {
      const playerStats = currentStats[playerId] || {
        penalties: 0,
        penaltyCount: 0,
        fouls: 0,
        breaks: 0,
        jamsPlayed: 0,
        penaltyTypes: {},
        starPasses: 0,
      };

      const newStats: Stats = {
        ...currentStats,
        [playerId]: {
          ...playerStats,
          penalties: playerStats.penalties + (isEnteringPenalty ? 1 : -1),
          penaltyCount: isEnteringPenalty ? playerStats.penaltyCount + 1 : playerStats.penaltyCount,
        }
      };

      // Recalculer le total aprs mise  jour
      const newP1 = statsByPeriod?.P1 && statsByPeriod.P1[playerId] ? statsByPeriod.P1[playerId].penaltyCount || 0 : 0;
      const newP2 = statsByPeriod?.P2 && statsByPeriod.P2[playerId] ? statsByPeriod.P2[playerId].penaltyCount || 0 : 0;
      const newTotal = (currentPeriod === 2 ? newP2 + 1 : newP1 + 1) + (currentPeriod === 2 ? newP1 : newP2);
      
      // Expulsion  7 fautes au total
      if (isEnteringPenalty && newTotal >= 7) {
        const updatedRoster = roster.map(p => 
          p.id === playerId ? { ...p, isAvailable: false } : p
        );
        window.dispatchEvent(new CustomEvent('updateRoster', { detail: updatedRoster }));
        
        alert(` EXPULSION \n\n${player?.name} (#${player?.number}) a atteint 7 pnalits au total et est expuls(e) du match !`);
      }

      return newStats as Stats;
    });
  };

  const toggleBreak = (playerId: string) => {
    setBreakPlayers((current) => {
      const newBreaks = current.includes(playerId)
        ? current.filter((id) => id !== playerId)
        : [...current, playerId];
      return newBreaks;
    });

    setStats((currentStats: Stats) => {
      const playerStats = currentStats[playerId] || {
        penalties: 0,
        penaltyCount: 0,
        fouls: 0,
        breaks: 0,
        jamsPlayed: 0,
        penaltyTypes: {}
      };

      const newStats: Stats = {
        ...currentStats,
        [playerId]: {
          ...playerStats,
          breaks: playerStats.breaks + (breakPlayers.includes(playerId) ? -1 : 1),
        }
      };

      return newStats as Stats;
    });
  };

  const updateCurrentLine = (position: keyof Line, playerId: string) => {
    if (playerId && isPlayerInLine(playerId, currentLineOverride)) {
      const currentPosition = Object.entries(currentLineOverride)
        .find(([_, id]) => id === playerId)?.[0];
      
      if (currentPosition !== position) {
        alert('Ce joueur est dj dans la ligne  une autre position.');
        return;
      }
    }

    setCurrentLineOverride(prev => ({
      ...prev,
      [position]: playerId
    }));
  };

  const updateNextLine = (position: keyof Line, playerId: string) => {
    if (playerId && Object.entries(currentLine).some(([curPos, curId]) => {
      const basePosition = position.replace(/\d+$/, '');
      const baseCurrentPos = curPos.replace(/\d+$/, '');
      return basePosition === baseCurrentPos && inPenalty.includes(curId);
    })) {
      alert('Ce joueur est en pnalit dans la ligne actuelle.');
      return;
    }

    if (playerId && isPlayerInLine(playerId, nextLineOverride)) {
      const currentPosition = Object.entries(nextLineOverride)
        .find(([_, id]) => id === playerId)?.[0];
      
      if (currentPosition !== position) {
        alert('Ce joueur est dj dans la ligne  une autre position.');
        return;
      }
    }

    setNextLineOverride(prev => ({
      ...prev,
      [position]: playerId
    }));
  };

  const handleStarPass = () => {
    if (starPassDone) return;
    const jammerId = currentLine.jammer;
    const pivotId = currentLine.pivot;
    if (!jammerId || !pivotId) return;

    // Swap roles in current line without triggering duplicate validation
    setCurrentLineOverride(prev => ({
      ...prev,
      jammer: pivotId,
      pivot: jammerId,
    }));

    // Increment star pass stat for jammer (who gave the star)
    setStats((currentStats: Stats) => {
      const jammerStats = currentStats[jammerId] || {
        penalties: 0,
        penaltyCount: 0,
        fouls: 0,
        breaks: 0,
        jamsPlayed: 0,
        penaltyTypes: {},
        starPasses: 0,
      };

      const newStats: Stats = {
        ...currentStats,
        [jammerId]: {
          ...jammerStats,
          starPasses: (jammerStats.starPasses || 0) + 1,
        },
      };

      return newStats as Stats;
    });

    setStarPassDone(true);
  };

  const renderPlayerOption = (player: Player) => (
    <option key={player.id} value={player.id}>
      {player.name} #{player.number}
    </option>
  );

  const renderPlayerSelect = (
    position: keyof Line,
    value: string,
    onChange: (position: keyof Line, value: string) => void,
    playerType: 'jammers' | 'pivots' | 'blockers'
  ) => {
    const isInvalid = !value;
    const player = value ? roster.find(p => p.id === value) : null;
    const showUnavailableWarning = player && (!player.isAvailable || player.onBreak);
    
    return (
      <select
        value={value} // Toujours utiliser la valeur actuelle, mme si le joueur n'est pas disponible
        onChange={(e) => onChange(position, e.target.value)}
        className={`block w-full rounded-md shadow-sm focus:border-indigo-500 focus:ring-indigo-500 ${
          isInvalid ? 'border-red-500 bg-red-50' : 'border-gray-300'
        }`}
      >
        <option value="" className="text-red-500">Select Player</option>
        {/* Ajouter l'option pour le joueur actuellement slectionn en premier, s'il existe */}
        {player && (
          <option key={player.id} value={player.id}>
            {player.name} #{player.number} {(!player.isAvailable || player.onBreak) ? '' : ''}
          </option>
        )}
        {sortedPlayers[playerType]
          .filter(p => {
            // Ignorer le joueur actuellement slectionn car on l'a dj ajout
            if (p.id === value) return false;
            
            // Vrifier que le joueur est disponible
            if (!p.isAvailable) return false;
            
            // Vrifier que le joueur n'est pas dj dans une position
            const lineToCheck = position.startsWith('jammer') || position.startsWith('pivot') ? currentLine : nextLine;
            if (Object.values(lineToCheck).includes(p.id)) return false;
            
            // Vrifier que le joueur n'est pas en pnalit
            if (inPenalty.includes(p.id)) return false;
            
            return true;
          })
          .map(renderPlayerOption)}
      </select>
    );
  };

  const renderPlayerStats = (playerId: string) => {
    if (!playerId) return null;
    const player = roster.find(p => p.id === playerId);
    if (!player) return null;

    // Calculer les totaux pour les statistiques
    const p1 = statsByPeriod?.P1 && statsByPeriod.P1[playerId] ? statsByPeriod.P1[playerId].penaltyCount || 0 : 0;
    const p2 = statsByPeriod?.P2 && statsByPeriod.P2[playerId] ? statsByPeriod.P2[playerId].penaltyCount || 0 : 0;
    const totalPenalties = p1 + p2;
    
    // Rcuprer le nombre de breaks
    const breakCount = stats[playerId]?.breaks || 0;

    return (
      <div className="text-center space-y-1 mt-2">
        {/* Afficher les stats du joueur */}
        <div className="text-xs bg-gray-100 py-1 px-2 rounded flex justify-between">
          <span>PB: {totalPenalties}</span>
          <span>|</span>
          <span>Break: {breakCount}</span>
        </div>
      </div>
    );
  };

  const renderPlayerCard = (
    playerId: string | undefined,
    position: keyof Line,
    isNextLine: boolean = false
  ) => {
    // decide which update fn to use
    const onChange = isNextLine ? updateNextLine : updateCurrentLine;
    const playerType: 'jammers' | 'pivots' | 'blockers' = position === 'jammer'
      ? 'jammers'
      : position === 'pivot'
      ? 'pivots'
      : 'blockers';

    // empty slot -> just select
    if (!playerId) {
      return (
        <div className="p-4 rounded-lg bg-white shadow-sm">
          {renderPlayerSelect(position, '', onChange, playerType)}
        </div>
      );
    }

    const player = roster.find(p => p.id === playerId);
    if (!player) return null;

    const isPenalty = inPenalty.includes(playerId);
    const isBreak = breakPlayers.includes(playerId);

    // Calculer le total des fautes (P1+P2) pour le code couleur
    const p1 = statsByPeriod?.P1 && statsByPeriod.P1[playerId] ? statsByPeriod.P1[playerId].penaltyCount || 0 : 0;
    const p2 = statsByPeriod?.P2 && statsByPeriod.P2[playerId] ? statsByPeriod.P2[playerId].penaltyCount || 0 : 0;
    const totalPenalties = p1 + p2;

    // Dterminer la classe de couleur du bouton PB en fonction du nombre de fautes
    const getPenaltyButtonClass = () => {
      if (isPenalty) return 'bg-green-500 text-white hover:bg-green-600'; // En pnalit -> bouton vert
      if (totalPenalties >= 7) return 'bg-red-700 text-white hover:bg-red-800'; // 7+ fautes -> rouge fonc
      if (totalPenalties >= 6) return 'bg-red-500 text-white hover:bg-red-600'; // 6 fautes -> rouge
      if (totalPenalties >= 5) return 'bg-orange-500 text-white hover:bg-orange-600'; // 5 fautes -> orange
      return 'bg-gray-100 text-gray-800 hover:bg-gray-200'; // < 5 fautes -> blanc/gris
    };

    return (
      <div className={`p-4 rounded-lg shadow-sm ${isPenalty ? 'bg-red-100' : isBreak ? 'bg-yellow-100' : 'bg-white'}`}>
        {/* Ajouter les marqueurs  ct du select */}
        <div className="flex items-center mb-2">
          {renderPlayerSelect(position, playerId, onChange, playerType)}
          {renderMarkers(playerId)}
        </div>

        {/* PB button avec la couleur qui change selon le nombre de fautes */}
        {!isNextLine && (
          <button
            onClick={() => togglePenalty(playerId)}
            className={`mt-1 px-2 py-3 rounded-md text-xs w-full ${getPenaltyButtonClass()}`}
          >
            {isPenalty ? 'EXIT PB' : `PB ${totalPenalties > 0 ? totalPenalties : ''}`}
            {totalPenalties >= 6 && totalPenalties < 7 && ' '} {/* Alerte pour 6 fautes */}
            {totalPenalties >= 7 && ' '} {/* X pour 7+ fautes */}
          </button>
        )}
        {isPenalty && renderPenaltyTypeSelect(playerId)}

        {/* Break button */}
        {isNextLine && !isPenalty && Object.entries(currentLine).some(([curPos, curId]) => {
          const basePosition = position.replace(/\d+$/, '');
          const baseCurrentPos = curPos.replace(/\d+$/, '');
          return basePosition === baseCurrentPos && inPenalty.includes(curId);
        }) && (
          <button
            onClick={() => toggleBreak(playerId)}
            className={`mt-1 px-2 py-3 rounded-md text-xs w-full ${isBreak ? 'bg-blue-500 text-white hover:bg-blue-600' : 'bg-yellow-500 text-white hover:bg-yellow-600'}`}
          >
            {isBreak ? 'CANCEL BREAK' : 'BREAK'}
          </button>
        )}

        {/* Star pass */}
        {position === 'jammer' && !isNextLine && currentLine.jammer && currentLine.pivot && !isPenalty && (
          <button
            onClick={handleStarPass}
            className="mt-1 px-2 py-3 rounded-md text-xs w-full bg-blue-500 text-white hover:bg-blue-600"
          >
            SP
          </button>
        )}
        
        {/* Stats du joueur */}
        {renderPlayerStats(playerId)}
      </div>
    );
  };

  const renderMarkers = (playerId: string) => {
    const player = roster.find(p => p.id === playerId);
    if (!player) return null;
    
    return (
      <div className="flex items-center space-x-1 ml-1">
        {player.marker1 && (
          <div className="w-3 h-3 rounded-full" style={{ backgroundColor: player.marker1 }} />
        )}
        {player.marker2 && (
          <div className="w-3 h-3 rounded-full" style={{ backgroundColor: player.marker2 }} />
        )}
        {player.marker3 && (
          <div className="w-3 h-3 rounded-full" style={{ backgroundColor: player.marker3 }} />
        )}
      </div>
    );
  };

  const renderPenaltyTypeSelect = (playerId: string) => {
    const pc = stats[playerId]?.penaltyCount || 0;
    if (pc === 0) return null;
    const lastPenaltyIndex = pc - 1;
    const playerStats = stats[playerId];
    const selectedPenalty = playerStats?.penaltyTypes?.[lastPenaltyIndex] || '';

    return (
      <select
        value={selectedPenalty}
        onChange={(e) => updatePenaltyType(playerId, e.target.value)}
        className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 text-xs"
      >
        <option value="">Type</option>
        {Object.entries(PENALTY_TYPES).map(([key, label]) => (
          <option key={key} value={key}>
            {key} - {label}
          </option>
        ))}
      </select>
    );
  };

  const startPeriod = (period: number) => {
    const resetLines = window.confirm(`Voulez-vous rinitialiser les lignes au dmarrage de la priode ${period}? \nCela remettra la ligne 1 en current et la ligne 2 en next.`);
    
    if (resetLines) {
      // Rinitialiser les indices de ligne
      setCurrentLineIndex(0);
      setCurrentLineOverride({});
      setNextLineOverride({});
    }
    
    setCurrentPeriod(period);
    
    if (period === 2) {
      // reset breaks
      const updatedRoster = roster.map(p => ({ ...p, onBreak: false }));
      window.dispatchEvent(new CustomEvent('updateRoster', { detail: updatedRoster }));
    }
  };

  return (
    <div className="space-y-8">
      <div className="flex items-center justify-between">
        <h1 className="text-3xl font-bold">
          Game - Jam {jamCount}
        </h1>
        <div className="space-x-4 flex items-center">
          {currentPeriod === 0 && (
            <button
              onClick={() => startPeriod(1)}
              className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-green-600 hover:bg-green-700"
            >
              Start P1
            </button>
          )}
          {currentPeriod === 1 && (
            <button
              onClick={() => startPeriod(2)}
              className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-green-600 hover:bg-green-700"
            >
              Start P2
            </button>
          )}
          <span className="text-lg font-semibold">Priode : {currentPeriod || '-'} / 2</span>
          <button
            onClick={resetGame}
            className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-orange-600 hover:bg-orange-700"
            title="Reset period stats while keeping jam count"
          >
            <Trash2 size={18} className="mr-2" />
            Reset Period
          </button>
          {onResetMatch && (
            <button
              onClick={() => {
                if (window.confirm('Are you sure you want to reset the match? This will reset all stats, jam count, and positions.')) {
                  onResetMatch();
                }
              }}
              className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-red-600 hover:bg-red-700 mr-2"
            >
              Reset Match
            </button>
          )}
        </div>
      </div>
      <div className="flex items-center space-x-8 mt-4">
        <button
          onClick={nextJam}
          className="inline-flex items-center px-4 py-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700"
        >
          <Plus size={18} className="mr-2" />
          Next Jam
        </button>
        <div className="flex space-x-2">
          {usedTTOs.map((used, index) => (
            <button
              key={`tto-${index}`}
              onClick={() => toggleTTO(index)}
              className={`px-5 py-2 rounded-md font-bold text-lg ${
                used
                  ? 'bg-gray-200 text-gray-500'
                  : 'bg-pink-600 text-white hover:bg-pink-700'
              }`}
            >
              TTO
            </button>
          ))}
        </div>
        <div className="flex space-x-2">
          {usedORs.map((used, index) => (
            <button
              key={`or-${index}`}
              onClick={() => toggleOR(index)}
              className={`px-5 py-2 rounded-md font-bold text-lg ${
                used
                  ? 'bg-gray-200 text-gray-500'
                  : 'bg-indigo-600 text-white hover:bg-indigo-700'
              }`}
            >
              OR
            </button>
          ))}
        </div>
      </div>

      <div className="bg-blue-100 rounded-lg shadow-lg p-6">
        <div className="mb-4">
          <h2 className="text-xl font-semibold">Current Line (Line #{currentLineIndex + 1})</h2>
        </div>
        <div className="grid grid-cols-5 gap-4">
          <div>
            <h3 className="text-sm font-medium text-gray-500 mb-2">Jammer</h3>
            {renderPlayerCard(currentLine.jammer, 'jammer')}
          </div>
          <div>
            <h3 className="text-sm font-medium text-gray-500 mb-2">Pivot</h3>
            {renderPlayerCard(currentLine.pivot, 'pivot')}
          </div>
          <div>
            <h3 className="text-sm font-medium text-gray-500 mb-2">Blocker 1</h3>
            {renderPlayerCard(currentLine.blocker1, 'blocker1')}
          </div>
          <div>
            <h3 className="text-sm font-medium text-gray-500 mb-2">Blocker 2</h3>
            {renderPlayerCard(currentLine.blocker2, 'blocker2')}
          </div>
          <div>
            <h3 className="text-sm font-medium text-gray-500 mb-2">Blocker 3</h3>
            {renderPlayerCard(currentLine.blocker3, 'blocker3')}
          </div>
        </div>
      </div>

      <div className="bg-green-100 rounded-lg shadow-lg p-6">
        <div className="mb-4 flex justify-between items-center">
          <h2 className="text-xl font-semibold">Next Line (Line #{((currentLineIndex + 1) % activeLines.length) + 1})</h2>
          <div className="flex items-center space-x-4">
            <button
              onClick={resetNextLine}
              className="inline-flex items-center px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50"
              title="Reset to original line composition"
            >
              <RotateCcw size={18} className="mr-2" />
              Reset Line
            </button>
            <button
              onClick={toggleAutoReset}
              className={`px-4 py-2 rounded-md font-medium ${
                autoResetEnabled
                  ? 'bg-blue-600 text-white hover:bg-blue-700'
                  : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
              }`}
            >
              {autoResetEnabled ? 'Auto-reset ON' : 'Auto-reset OFF'}
            </button>
            <button
              onClick={() => setJammerWaitForPB(!jammerWaitForPB)}
              className={`px-4 py-2 rounded-md font-medium ${
                jammerWaitForPB
                  ? 'bg-blue-600 text-white hover:bg-blue-700'
                  : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
              }`}
            >
              {jammerWaitForPB ? 'Jammer wait for PB' : 'Jammer don\'t wait for PB'}
            </button>
          </div>
        </div>

        <div className="mb-4 flex flex-wrap gap-2">
          {lines.map((line, index) => (
            
            <button
              key={line.id}
              onClick={() => copyLineToNext(line)}
              className={`px-3 py-1 rounded-md text-sm font-medium ${
                line.isDormant
                  ? 'bg-gray-100 text-gray-500'
                  : 'bg-indigo-100 text-indigo-700 hover:bg-indigo-200'
              }`}
              title={line.isDormant ? 'Cette ligne est en pause' : undefined}
            >
              {line.name ||`Line ${index + 1}`}
            </button>
          ))}
        </div>

        <div className="grid grid-cols-5 gap-4">
          <div>
            <h3 className="text-sm font-medium text-gray-500 mb-2">Jammer</h3>
            {renderPlayerCard(nextLine.jammer, 'jammer', true)}
          </div>
          <div>
            <h3 className="text-sm font-medium text-gray-500 mb-2">Pivot</h3>
            {renderPlayerCard(nextLine.pivot, 'pivot', true)}
          </div>
          <div>
            <h3 className="text-sm font-medium text-gray-500 mb-2">Blocker 1</h3>
            {renderPlayerCard(nextLine.blocker1, 'blocker1', true)}
          </div>
          <div>
            <h3 className="text-sm font-medium text-gray-500 mb-2">Blocker 2</h3>
            {renderPlayerCard(nextLine.blocker2, 'blocker2', true)}
          </div>
          <div>
            <h3 className="text-sm font-medium text-gray-500 mb-2">Blocker 3</h3>
            {renderPlayerCard(nextLine.blocker3, 'blocker3', true)}
          </div>
        </div>
      </div>

      <div className="bg-white rounded-lg shadow-lg p-4">
        <h2 className="text-lg font-bold mb-2">Jammer Line List</h2>
        <div className="grid grid-cols-4 gap-2">
          {jammerListLine.map((jammerLine) => {
            const player = roster.find(p => p.id === jammerLine.jammer);
            const isCurrentJammer = currentLine.jammer === jammerLine.jammer;
            const isNextJammer = nextLine.jammer === jammerLine.jammer;
            
            return (
              <div 
                key={jammerLine.id} 
                className={`px-3 py-2 rounded-lg ${
                  isCurrentJammer 
                    ? 'bg-indigo-100 border-l-2 border-indigo-500'
                    : isNextJammer
                    ? 'bg-green-100 border-l-2 border-green-500'
                    : 'bg-gray-50'
                }`}
              >
                <div className="font-medium text-sm">{player?.name}</div>
                <div className="text-xs text-gray-500">#{player?.number}</div>
                <div className="text-xs text-gray-600 mt-1">
                  Positions: {(jammerLine.positions || [1]).join(', ')}
                </div>
                {(isCurrentJammer || isNextJammer) && (
                  <div className={`text-xs font-medium ${isCurrentJammer ? 'text-indigo-600' : 'text-green-600'}`}>
                    {isCurrentJammer ? 'Current' : 'Next'}
                  </div>
                )}
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}

export { GamePage };