import React, { useState, useEffect } from 'react';
import { useLocalStorage } from '../hooks/useLocalStorage';
import { useTranslation } from 'react-i18next';
import {
  QrCode,
  Camera,
  Star,
  Timer,
  Activity,
  Edit,
  Trash2
} from 'lucide-react';
import { QRCodeSVG } from 'qrcode.react';
import QrScanner from 'react-qr-scanner';
import { Player } from '../types';

// Nouvelles interfaces pour les statistiques dtailles
interface JamStat {
  jamNumber: number;
  points: number;
  lead: boolean;
  duration: number; // en secondes
  calls: {
    timeout: boolean;
    review: boolean;
  };
}

interface JammerStat {
  playerId: string;
  jams: JamStat[];
  total: {
    points: number;
    leads: number;
    avgPoints: number;
  };
}

interface PeriodDetailedStats {
  period: number;
  jammers: Record<string, JammerStat>;
  lastUpdate: string;
}

interface DetailedStats {
  P1: PeriodDetailedStats;
  P2: PeriodDetailedStats;
}

interface StatsDetailPageProps {
  roster: Player[];
}

export function StatsDetailPage({
  roster
}: StatsDetailPageProps) {
  const { t } = useTranslation();
  const [view, setView] = useState<'P1' | 'P2' | 'TOTAL'>('P1');
  
  // tats et effets du composant - utilisation de useLocalStorage pour persister les donnes
  const [detailedStats, setDetailedStats] = useLocalStorage<DetailedStats>('detailedStats', {
    P1: {
      period: 1,
      jammers: {},
      lastUpdate: new Date().toISOString()
    },
    P2: {
      period: 2,
      jammers: {},
      lastUpdate: new Date().toISOString()
    }
  });
  const [showQRCode, setShowQRCode] = useState<boolean>(false);
  const [qrValue, setQrValue] = useState<string>('');
  const [showQRScanner, setShowQRScanner] = useState<boolean>(false);
  const [scanError, setScanError] = useState<string | null>(null);
  const [currentCamera, setCurrentCamera] = useState<'environment' | 'user'>('environment'); // 'environment' = camra arrire, 'user' = camra avant
  
  // Gestion du formulaire de saisie
  const [jamForm, setJamForm] = useState({
    jamNumber: 1,
    selectedJammerId: '',
    customJammerName: '',
    points: 0,
    lead: false,
    minutes: 1,
    seconds: 30,
    isEditing: false,
    editingJamId: ''
  });

  // tat du chronomtre
  const [stopwatch, setStopwatch] = useState({
    running: false,
    startTime: 0,
    elapsedTime: 0
  });

  // Fonctions de gestion du formulaire
  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value, type } = e.target as HTMLInputElement;
    
    setJamForm(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? (e.target as HTMLInputElement).checked : 
             type === 'number' ? parseInt(value) || 0 : value
    }));
  };
  
  // Fonctions de gestion des points
  const handlePointsChange = (change: number) => {
    setJamForm(prev => ({
      ...prev,
      points: Math.max(0, prev.points + change) // Assurez-vous que les points ne soient pas ngatifs
    }));
  };

  // Ajouter ou modifier un jam
  const saveJam = () => {
    // Vrifier que les donnes sont valides
    if (!jamForm.selectedJammerId && !jamForm.customJammerName) {
      alert(t('selectJammer'));
      return;
    }
    
    // Calculer la dure totale en secondes
    const duration = (jamForm.minutes * 60) + jamForm.seconds;
    
    // Crer l'objet jam
    const jamData: JamStat = {
      jamNumber: jamForm.jamNumber,
      points: jamForm.points,
      lead: jamForm.lead,
      duration,
      calls: {
        timeout: false,
        review: false
      }
    };
    
    setDetailedStats(prev => {
      const currentView = view as 'P1' | 'P2';
      const jammerId = jamForm.selectedJammerId || jamForm.customJammerName;
      
      // Copier les jammers actuels
      const updatedJammers = { ...prev[currentView].jammers };
      
      // Si le jammer existe dj
      if (updatedJammers[jammerId]) {
        let jams = [...updatedJammers[jammerId].jams];
        
        // Mode dition: remplacer le jam existant
        if (jamForm.isEditing) {
          jams = jams.map(jam => 
            jam.jamNumber === jamForm.jamNumber ? jamData : jam
          );
        } else {
          // Mode ajout: ajouter un nouveau jam
          jams.push(jamData);
        }
        
        // Recalculer les totaux
        const points = jams.reduce((sum, jam) => sum + jam.points, 0);
        const leads = jams.reduce((sum, jam) => sum + (jam.lead ? 1 : 0), 0);
        
        updatedJammers[jammerId] = {
          ...updatedJammers[jammerId],
          jams,
          total: {
            points,
            leads,
            avgPoints: jams.length ? points / jams.length : 0
          }
        };
      } 
      // Si le jammer n'existe pas encore
      else {
        updatedJammers[jammerId] = {
          playerId: jammerId,
          jams: [jamData],
          total: {
            points: jamData.points,
            leads: jamData.lead ? 1 : 0,
            avgPoints: jamData.points
          }
        };
      }
      
      // Mettre  jour la priode actuelle
      return {
        ...prev,
        [currentView]: {
          ...prev[currentView],
          jammers: updatedJammers,
          lastUpdate: new Date().toISOString()
        }
      };
    });
    
    // Rinitialiser le formulaire pour le prochain jam
    setJamForm(prev => ({
      ...prev,
      jamNumber: prev.jamNumber + 1,
      points: 0,
      lead: false,
      minutes: 1,
      seconds: 30,
      isEditing: false,
      editingJamId: ''
    }));
  };
  
  // Fonction pour diter un jam existant
  const editJam = (jammerId: string, jamNumber: number) => {
    // Trouver le jam dans les donnes
    const currentView = view as 'P1' | 'P2';
    const jammer = detailedStats[currentView].jammers[jammerId];
    const jam = jammer?.jams.find(j => j.jamNumber === jamNumber);
    
    if (jam) {
      const minutes = Math.floor(jam.duration / 60);
      const seconds = jam.duration % 60;
      
      setJamForm({
        jamNumber: jam.jamNumber,
        selectedJammerId: jammerId,
        customJammerName: '',
        points: jam.points,
        lead: jam.lead,
        minutes,
        seconds,
        isEditing: true,
        editingJamId: `${jammerId}-${jamNumber}`
      });
    }
  };
  
  // Fonction pour supprimer un jam
  const deleteJam = (jammerId: string, jamNumber: number) => {
    if (window.confirm(t('confirmDeleteJam'))) {
      setDetailedStats(prev => {
        const currentView = view as 'P1' | 'P2';
        const jammer = { ...prev[currentView].jammers[jammerId] };
        
        // Filtrer pour supprimer le jam spcifique
        const updatedJams = jammer.jams.filter(jam => jam.jamNumber !== jamNumber);
        
        // Si plus aucun jam, supprimer le jammer
        if (updatedJams.length === 0) {
          const { [jammerId]: _, ...restJammers } = prev[currentView].jammers;
          
          return {
            ...prev,
            [currentView]: {
              ...prev[currentView],
              jammers: restJammers,
              lastUpdate: new Date().toISOString()
            }
          };
        }
        
        // Sinon, mettre  jour les jams et recalculer les totaux
        const points = updatedJams.reduce((sum, jam) => sum + jam.points, 0);
        const leads = updatedJams.reduce((sum, jam) => sum + (jam.lead ? 1 : 0), 0);
        
        return {
          ...prev,
          [currentView]: {
            ...prev[currentView],
            jammers: {
              ...prev[currentView].jammers,
              [jammerId]: {
                ...jammer,
                jams: updatedJams,
                total: {
                  points,
                  leads,
                  avgPoints: updatedJams.length ? points / updatedJams.length : 0
                }
              }
            },
            lastUpdate: new Date().toISOString()
          }
        };
      });
    }
  };

  // Fonctions du chronomtre
  const startStopwatch = () => {
    if (!stopwatch.running) {
      setStopwatch(prev => ({
        ...prev,
        running: true,
        startTime: Date.now() - prev.elapsedTime
      }));
    }
  };

  const stopStopwatch = () => {
    if (stopwatch.running) {
      setStopwatch(prev => ({
        ...prev,
        running: false,
        elapsedTime: Date.now() - prev.startTime
      }));
    }
  };

  const resetStopwatch = () => {
    setStopwatch({
      running: false,
      startTime: 0,
      elapsedTime: 0
    });
  };

  const useStopwatchTime = () => {
    const totalSeconds = Math.floor(stopwatch.elapsedTime / 1000);
    setJamForm(prev => ({
      ...prev,
      minutes: Math.floor(totalSeconds / 60),
      seconds: totalSeconds % 60
    }));
  };

  // Mise  jour du chronomtre
  useEffect(() => {
    let interval: NodeJS.Timeout | null = null;
    
    if (stopwatch.running) {
      interval = setInterval(() => {
        setStopwatch(prev => ({
          ...prev,
          elapsedTime: Date.now() - prev.startTime
        }));
      }, 10);
    }
    
    return () => {
      if (interval) clearInterval(interval);
    };
  }, [stopwatch.running]);

  // Utilisez useEffect pour mettre  jour le numro de jam lorsque la vue change
  useEffect(() => {
    // Calculer le prochain numro de jam  partir des donnes existantes
    if (view === 'P1' || view === 'P2') {
      const jammers = detailedStats[view].jammers;
      let maxJamNumber = 0;
      
      Object.values(jammers).forEach(jammer => {
        jammer.jams.forEach(jam => {
          if (jam.jamNumber > maxJamNumber) {
            maxJamNumber = jam.jamNumber;
          }
        });
      });
      
      setJamForm(prev => ({
        ...prev,
        jamNumber: maxJamNumber + 1
      }));
    }
  }, [view, detailedStats]);
  
  // Fonction pour gnrer le QR code
  const generateQRCode = () => {
    try {
      // Slectionner les donnes actuelles en fonction de la priode slectionne
      const dataToEncode = {
        type: 'rdlm-stats',
        view,
        data: view === 'TOTAL' 
          ? { jammers: Object.keys({...detailedStats.P1.jammers, ...detailedStats.P2.jammers}) } 
          : detailedStats[view as 'P1' | 'P2'],
        timestamp: new Date().toISOString()
      };
      
      // Convertir en JSON et compresser pour le QR code
      const jsonStr = JSON.stringify(dataToEncode);
      setQrValue(jsonStr);
      setShowQRCode(true);
    } catch (error) {
      console.error('Error generating QR code:', error);
      alert(t('qrGenerationError'));
    }
  };
  
  // Fonction pour fermer le QR code
  const closeQRCode = () => {
    setShowQRCode(false);
  };
  
  // Fonction pour scanner le QR code
  const scanQRCode = () => {
    setScanError(null);
    setShowQRScanner(true);
  };
  
  // Fonction de gestion des donnes du QR scanner
  const handleScan = (data: string | null) => {
    if (data) {
      try {
        const parsedData = JSON.parse(data);
        
        if (parsedData.type === 'rdlm-stats') {
          // Fusion des donnes reues
          setDetailedStats(prev => {
            // Si les donnes concernent la priode 1
            if (parsedData.view === 'P1' && parsedData.data && parsedData.data.jammers) {
              const updatedP1 = {
                ...prev.P1,
                jammers: {
                  ...prev.P1.jammers,
                  ...parsedData.data.jammers
                },
                lastUpdate: parsedData.timestamp || new Date().toISOString()
              };
              
              return {
                ...prev,
                P1: updatedP1
              };
            } 
            // Si les donnes concernent la priode 2
            else if (parsedData.view === 'P2' && parsedData.data && parsedData.data.jammers) {
              const updatedP2 = {
                ...prev.P2,
                jammers: {
                  ...prev.P2.jammers,
                  ...parsedData.data.jammers
                },
                lastUpdate: parsedData.timestamp || new Date().toISOString()
              };
              
              return {
                ...prev,
                P2: updatedP2
              };
            }
            
            return prev;
          });
          
          // Fermer le scanner et afficher un message de succs
          setShowQRScanner(false);
          alert(t('qrScanSuccess'));
        } else {
          setScanError(t('invalidQRCode'));
        }
      } catch (error) {
        console.error('Error parsing QR data:', error);
        setScanError(t('qrParseError'));
      }
    }
  };
  
  // Gestion des erreurs du scanner
  const handleScanError = (error: Error) => {
    console.error('QR scan error:', error);
    setScanError(t('qrScanError'));
  };
  
  // Fermeture du scanner
  const closeScanner = () => {
    setShowQRScanner(false);
  };
  
  // Fonction pour rinitialiser les statistiques dtailles
  const resetDetailedStats = () => {
    if (window.confirm(t('confirmResetStats'))) {
      setDetailedStats({
        P1: {
          period: 1,
          jammers: {},
          lastUpdate: new Date().toISOString()
        },
        P2: {
          period: 2,
          jammers: {},
          lastUpdate: new Date().toISOString()
        }
      });
      
      // Rinitialiser le formulaire
      setJamForm(prev => ({
        ...prev,
        jamNumber: 1,
        selectedJammerId: '',
        customJammerName: '',
        points: 0,
        lead: false,
        minutes: 1,
        seconds: 30,
        isEditing: false,
        editingJamId: ''
      }));
    }
  };

  // Rendu de l'en-tte avec slecteurs de priode
  const renderHeader = () => (
    <div className="mb-6">
      <h1 className="text-2xl font-bold mb-4">Detailed Stats</h1>
      
      <div className="flex space-x-2 mb-4">
        <button
          onClick={() => setView('P1')}
          className={`px-4 py-2 rounded-md ${view === 'P1' ? 'bg-indigo-600 text-white' : 'bg-gray-200 text-gray-700'}`}
        >
          {t('period')} 1
        </button>
        <button
          onClick={() => setView('P2')}
          className={`px-4 py-2 rounded-md ${view === 'P2' ? 'bg-indigo-600 text-white' : 'bg-gray-200 text-gray-700'}`}
        >
          {t('period')} 2
        </button>
        <button
          onClick={() => setView('TOTAL')}
          className={`px-4 py-2 rounded-md ${view === 'TOTAL' ? 'bg-indigo-600 text-white' : 'bg-gray-200 text-gray-700'}`}
        >
          {t('total')}
        </button>
      </div>
      
      <div className="flex justify-between items-center mb-4">
        <div className="text-sm text-gray-500">
          {t('lastUpdate')}: {new Date(detailedStats[view === 'TOTAL' ? 'P2' : view].lastUpdate).toLocaleTimeString()}
        </div>
        
        <div className="flex space-x-2">
          <button
            className="flex items-center gap-1 px-3 py-1 bg-green-500 text-white rounded-md hover:bg-green-600"
            onClick={generateQRCode}
          >
            <QrCode size={16} />
            {t('generateQR')}
          </button>
          
          <button
            className="flex items-center gap-1 px-3 py-1 bg-blue-500 text-white rounded-md hover:bg-blue-600"
            onClick={scanQRCode}
          >
            <Camera size={16} />
            {t('scanQR')}
          </button>
          <button
            className="flex items-center gap-1 px-3 py-1 bg-red-500 text-white rounded-md hover:bg-red-600"
            onClick={resetDetailedStats}
          >
            {t('resetStats')}
          </button>
        </div>
      </div>
    </div>
  );
  
  // Slection des donnes en fonction de la vue
  const currentViewData = view === 'TOTAL' 
    ? {
        jammers: Object.keys({...detailedStats.P1.jammers, ...detailedStats.P2.jammers}).reduce((acc, jammerId) => {
          const p1Data = detailedStats.P1.jammers[jammerId];
          const p2Data = detailedStats.P2.jammers[jammerId];
          
          if (!p1Data && !p2Data) return acc;
          
          const jams = [...(p1Data?.jams || []), ...(p2Data?.jams || [])];
          const points = (p1Data?.total.points || 0) + (p2Data?.total.points || 0);
          const leads = (p1Data?.total.leads || 0) + (p2Data?.total.leads || 0);
          
          acc[jammerId] = {
            playerId: jammerId,
            jams,
            total: {
              points,
              leads,
              avgPoints: jams.length ? points / jams.length : 0
            }
          };
          
          return acc;
        }, {} as Record<string, JammerStat>)
      }
    : detailedStats[view];
  
  // Rendu du formulaire de saisie
  const renderJamForm = () => {
    if (view === 'TOTAL') return null; // Ne pas afficher le formulaire en vue Total
    
    return (
      <div className="bg-white rounded-lg shadow p-4 mb-6">
        <h2 className="text-xl font-semibold text-gray-800 mb-4">
          {jamForm.isEditing ? t('editJamStats') : t('addJamStats')}
        </h2>
        
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-4">
          {/* Numro de jam */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              {t('jamNumber')}
            </label>
            <input
              type="number"
              name="jamNumber"
              value={jamForm.jamNumber}
              onChange={handleInputChange}
              className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
            />
          </div>
          
          {/* Slection du jammer */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              {t('jammer')}
            </label>
            <div className="flex flex-col">
              <select
                name="selectedJammerId"
                value={jamForm.selectedJammerId}
                onChange={handleInputChange}
                className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
              >
                <option value="">{t('selectJammer')}</option>
                {roster
                  .filter(player => player.isJammer)
                  .map(player => (
                    <option key={player.id} value={player.id}>
                      {player.name} (#{player.number})
                    </option>
                  ))}
              </select>
              <input
                type="text"
                name="customJammerName"
                value={jamForm.customJammerName}
                onChange={handleInputChange}
                placeholder={t('customJammerName')}
                className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 mt-2"
              />
            </div>
          </div>
          
          {/* Points */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              {t('points')}
            </label>
            <div className="flex items-center space-x-2">
              <input
                type="number"
                name="points"
                value={jamForm.points}
                onChange={handleInputChange}
                className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
              />
              <div className="flex space-x-1">
                <button
                  type="button"
                  onClick={() => handlePointsChange(-1)}
                  className="px-2 py-1 bg-red-500 text-white rounded-l-md hover:bg-red-600"
                  title="Remove 1 point"
                >
                  -1
                </button>
                <button
                  type="button"
                  onClick={() => handlePointsChange(1)}
                  className="px-2 py-1 bg-blue-500 text-white hover:bg-blue-600"
                  title="Add 1 point"
                >
                  +1
                </button>
                <button
                  type="button"
                  onClick={() => handlePointsChange(4)}
                  className="px-2 py-1 bg-green-500 text-white rounded-r-md hover:bg-green-600"
                  title="Add 4 points"
                >
                  +4
                </button>
              </div>
            </div>
          </div>
          
          {/* Lead Jammer */}
          <div className="flex items-end mb-2">
            <label className="flex items-center space-x-2 text-sm font-medium text-gray-700">
              <input
                type="checkbox"
                name="lead"
                checked={jamForm.lead}
                onChange={handleInputChange}
                className="h-4 w-4 text-indigo-600 border-gray-300 rounded focus:ring-indigo-500"
              />
              <span>{t('lead')}</span>
            </label>
          </div>
        </div>
        
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-4">
          {/* Dure */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              {t('duration')}
            </label>
            <div className="flex items-center space-x-2">
              <div className="flex-1">
                <input
                  type="number"
                  name="minutes"
                  value={jamForm.minutes}
                  onChange={handleInputChange}
                  min="0"
                  max="10"
                  className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                />
                <span className="text-xs text-gray-500">{t('minutesLabel')}</span>
              </div>
              <span className="text-xl font-bold">:</span>
              <div className="flex-1">
                <input
                  type="number"
                  name="seconds"
                  value={jamForm.seconds}
                  onChange={handleInputChange}
                  min="0"
                  max="59"
                  className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                />
                <span className="text-xs text-gray-500">{t('secondsLabel')}</span>
              </div>
            </div>
          </div>
        </div>
        
        {/* Boutons d'action rapide */}
        <div className="mb-4">
          <h3 className="text-sm font-medium text-gray-700 mb-2">{t('quickButtons')}</h3>
          <div className="flex flex-wrap gap-2">
            <button 
              type="button"
              onClick={() => setJamForm(prev => ({ ...prev, lead: !prev.lead }))}
              className={`px-3 py-1 rounded-full text-sm ${jamForm.lead ? 'bg-yellow-400 text-yellow-900' : 'bg-gray-100 text-gray-800'}`}
            >
              {jamForm.lead ? 'Is Lead ' : 'Lead'}
            </button>
            
            {/* Prrglages de dure */}
            <button
              type="button"
              onClick={() => setJamForm(prev => ({ ...prev, minutes: 2, seconds: 0 }))}
              className="px-3 py-1 bg-gray-100 text-gray-800 rounded-full text-sm"
            >
              2:00
            </button>
            <button
              type="button"
              onClick={() => setJamForm(prev => ({ ...prev, minutes: 1, seconds: 30 }))}
              className="px-3 py-1 bg-gray-100 text-gray-800 rounded-full text-sm"
            >
              1:30
            </button>
            <button
              type="button"
              onClick={() => setJamForm(prev => ({ ...prev, minutes: 1, seconds: 0 }))}
              className="px-3 py-1 bg-gray-100 text-gray-800 rounded-full text-sm"
            >
              1:00
            </button>
          </div>
        </div>
        
        <div className="flex justify-end space-x-2">
          {jamForm.isEditing && (
            <button
              type="button"
              onClick={() => setJamForm({
                jamNumber: Math.max(...Object.values(detailedStats[view as 'P1' | 'P2'].jammers).flatMap(j => j.jams.map(jam => jam.jamNumber)), 0) + 1,
                selectedJammerId: '',
                customJammerName: '',
                points: 0,
                lead: false,
                minutes: 1,
                seconds: 30,
                isEditing: false,
                editingJamId: ''
              })}
              className="px-4 py-2 bg-gray-200 text-gray-700 rounded-md hover:bg-gray-300"
            >
              {t('cancelEdit')}
            </button>
          )}
          <button
            type="button"
            onClick={saveJam}
            className="px-4 py-2 bg-indigo-600 text-white rounded-md hover:bg-indigo-700"
          >
            {t('saveJam')}
          </button>
        </div>
      </div>
    );
  };
  
  // Rendu du tableau des jammers
  const renderJammerTable = () => (
    <div className="bg-white rounded-lg shadow p-4 mb-6">
      <h2 className="text-xl font-semibold text-gray-800 mb-4 flex items-center">
        <Star className="mr-2 text-yellow-500" size={20} />
        {t('jammerPerformance')}
      </h2>
      
      <div className="overflow-x-auto">
        <table className="min-w-full">
          <thead>
            <tr className="bg-gray-100">
              <th className="px-4 py-2 text-left">{t('jammer')}</th>
              <th className="px-4 py-2 text-center">{t('jams')}</th>
              <th className="px-4 py-2 text-center">{t('points')}</th>
              <th className="px-4 py-2 text-center">{t('avgPoints')}</th>
              <th className="px-4 py-2 text-center">{t('leads')}</th>
              <th className="px-4 py-2 text-center">{t('leadPercentage')}</th>
            </tr>
          </thead>
          <tbody>
            {Object.values(currentViewData.jammers).map(jammer => {
              const player = roster.find(p => p.id === jammer.playerId) || { name: 'Unknown', number: '?' };
              const jammerId = jammer.playerId;
              const jamsCount = jammer.jams.length;
              const leadsPercentage = jamsCount ? (jammer.total.leads / jamsCount) * 100 : 0;
              
              return (
                <tr key={jammerId} className="border-t hover:bg-gray-50">
                  <td className="px-4 py-2">
                    <div className="font-medium">{player.name}</div>
                    <div className="text-sm text-gray-500">#{player.number}</div>
                  </td>
                  <td className="px-4 py-2 text-center">{jamsCount}</td>
                  <td className="px-4 py-2 text-center font-semibold">{jammer.total.points}</td>
                  <td className="px-4 py-2 text-center">{jammer.total.avgPoints.toFixed(1)}</td>
                  <td className="px-4 py-2 text-center">{jammer.total.leads}</td>
                  <td className="px-4 py-2 text-center">
                    <div className="flex items-center justify-center">
                      <div className="w-full max-w-[100px] bg-gray-200 rounded-full h-2.5">
                        <div 
                          className="bg-indigo-600 h-2.5 rounded-full" 
                          style={{ width: `${leadsPercentage}%` }}
                        ></div>
                      </div>
                      <span className="ml-2">{leadsPercentage.toFixed(0)}%</span>
                    </div>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    </div>
  );
  
  // Rendu du tableau des jams par jammer
  const renderJamDetailsTable = () => (
    <div className="bg-white rounded-lg shadow p-4 mb-6">
      <h2 className="text-xl font-semibold text-gray-800 mb-4 flex items-center">
        <Activity className="mr-2 text-green-500" size={20} />
        {t('jamByJam')}
      </h2>
      
      <div className="overflow-x-auto">
        <table className="min-w-full">
          <thead>
            <tr className="bg-gray-100">
              <th className="px-4 py-2 text-left">{t('jammer')}</th>
              <th className="px-4 py-2 text-center">{t('jamNumber')}</th>
              <th className="px-4 py-2 text-center">{t('lead')}</th>
              <th className="px-4 py-2 text-center">{t('points')}</th>
              <th className="px-4 py-2 text-center">{t('duration')}</th>
              <th className="px-4 py-2 text-center">{t('actions')}</th>
            </tr>
          </thead>
          <tbody>
            {Object.values(currentViewData.jammers).flatMap(jammer => {
              const player = roster.find(p => p.id === jammer.playerId) || { name: 'Unknown', number: '?' };
              
              return jammer.jams.map(jam => (
                <tr key={`${jammer.playerId}-${jam.jamNumber}`} className="border-t hover:bg-gray-50">
                  <td className="px-4 py-2">
                    <div className="font-medium">{player.name}</div>
                    <div className="text-sm text-gray-500">#{player.number}</div>
                  </td>
                  <td className="px-4 py-2 text-center">{jam.jamNumber}</td>
                  <td className="px-4 py-2 text-center">
                    {jam.lead ? (
                      <div className="inline-flex items-center justify-center bg-yellow-100 text-yellow-800 text-xs font-medium px-2 py-0.5 rounded-full">
                        <Star className="mr-1" size={12} />
                        Lead
                      </div>
                    ) : ''}
                  </td>
                  <td className="px-4 py-2 text-center font-semibold">{jam.points}</td>
                  <td className="px-4 py-2 text-center">
                    <div className="flex items-center justify-center">
                      <Timer className="mr-1 text-gray-400" size={14} />
                      {Math.floor(jam.duration / 60)}:{(jam.duration % 60).toString().padStart(2, '0')}
                    </div>
                  </td>
                  <td className="px-4 py-2 text-center">
                    <div className="flex space-x-1">
                      <button
                        type="button"
                        onClick={() => editJam(jammer.playerId, jam.jamNumber)}
                        className="px-2 py-1 bg-gray-100 text-gray-800 rounded-full text-xs font-medium"
                      >
                        <Edit className="inline-block w-3 h-3 mr-1" />
                      </button>
                      <button
                        type="button"
                        onClick={() => deleteJam(jammer.playerId, jam.jamNumber)}
                        className="px-2 py-1 bg-red-500 text-white rounded-full text-xs font-medium"
                      >
                        <Trash2 className="inline-block w-3 h-3 mr-1" />
                      </button>
                    </div>
                  </td>
                </tr>
              ));
            })}
          </tbody>
        </table>
      </div>
    </div>
  );
  
  // Chronomtre
  const renderStopwatch = () => (
    <div className="fixed bottom-16 left-0 right-0 bg-gray-800 text-white p-4 shadow-lg z-50">
      <div className="flex items-center justify-between max-w-6xl mx-auto">
        <div className="text-2xl font-bold">
          {Math.floor(stopwatch.elapsedTime / 1000 / 60).toString().padStart(2, '0')}:{(Math.floor(stopwatch.elapsedTime / 1000) % 60).toString().padStart(2, '0')}.{Math.floor((stopwatch.elapsedTime % 1000) / 10).toString().padStart(2, '0')}
        </div>
        <div className="flex space-x-2">
          <button
            type="button"
            onClick={startStopwatch}
            className={`px-4 py-2 ${stopwatch.running ? 'bg-gray-500' : 'bg-green-500'} text-white rounded-md hover:opacity-90`}
            disabled={stopwatch.running}
          >
            Start
          </button>
          <button
            type="button"
            onClick={stopStopwatch}
            className={`px-4 py-2 ${!stopwatch.running ? 'bg-gray-500' : 'bg-red-500'} text-white rounded-md hover:opacity-90`}
            disabled={!stopwatch.running}
          >
            Stop
          </button>
          <button
            type="button"
            onClick={resetStopwatch}
            className="px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-600"
          >
            Reset
          </button>
          <button
            type="button"
            onClick={useStopwatchTime}
            className="px-4 py-2 bg-indigo-500 text-white rounded-md hover:bg-indigo-600"
          >
            Use
          </button>
        </div>
      </div>
    </div>
  );
  
  return (
    <div className="max-w-7xl mx-auto p-4">
      {renderHeader()}
      {renderJamForm()}
      
      {/* Stats Summary Cards */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
        {/* Total Points Card */}
        <div className="bg-gradient-to-r from-purple-500 to-indigo-600 rounded-lg shadow p-4 text-white">
          <div className="font-medium text-purple-100 mb-1">{t('totalPoints')}</div>
          <div className="text-3xl font-bold">
            {Object.values(currentViewData.jammers).reduce((sum, jammer) => sum + jammer.total.points, 0)}
          </div>
        </div>
        
        {/* Lead Jammer Card */}
        <div className="bg-gradient-to-r from-yellow-400 to-orange-500 rounded-lg shadow p-4 text-white">
          <div className="font-medium text-yellow-100 mb-1">{t('totalLeads')}</div>
          <div className="text-3xl font-bold">
            {Object.values(currentViewData.jammers).reduce((sum, jammer) => sum + jammer.total.leads, 0)}
          </div>
        </div>
        
        {/* Average Points Card */}
        <div className="bg-gradient-to-r from-green-500 to-teal-500 rounded-lg shadow p-4 text-white">
          <div className="font-medium text-green-100 mb-1">{t('avgPointsPerJam')}</div>
          <div className="text-3xl font-bold">
            {
              (() => {
                const totalJams = Object.values(currentViewData.jammers).reduce(
                  (sum, jammer) => sum + jammer.jams.length, 0
                );
                const totalPoints = Object.values(currentViewData.jammers).reduce(
                  (sum, jammer) => sum + jammer.total.points, 0
                );
                return totalJams ? (totalPoints / totalJams).toFixed(1) : '0';
              })()
            }
          </div>
        </div>
      </div>
      
      {renderJammerTable()}
      {renderJamDetailsTable()}
      
      {showQRCode && (
        <div className="fixed inset-0 bg-gray-500 bg-opacity-75 flex items-center justify-center">
          <div className="bg-white rounded-lg shadow p-4">
            <h2 className="text-lg font-bold mb-2">{t('qrCode')}</h2>
            <QRCodeSVG value={qrValue} size={256} />
            <button
              className="mt-4 bg-red-500 text-white rounded-md px-4 py-2 hover:bg-red-600"
              onClick={closeQRCode}
            >
              {t('closeQR')}
            </button>
          </div>
        </div>
      )}
      
      {showQRScanner && (
        <div className="fixed inset-0 bg-gray-500 bg-opacity-75 flex items-center justify-center z-[9999]">
          <div className="bg-white rounded-lg shadow p-4 max-w-md w-full mx-auto max-h-[90vh] overflow-y-auto">
            <h2 className="text-lg font-bold mb-2">{t('scanQR')}</h2>
            <p className="text-sm text-gray-600 mb-2">
              {currentCamera === 'environment' ? 'Utilisation de la camra arrire' : 'Utilisation de la camra frontale'}
              <span className="ml-1 text-indigo-600">(Utilisez le bouton en haut  droite du scanner pour changer)</span>
            </p>
            <div className="relative" style={{ height: '300px' }}>
              <QrScanner
                onScan={handleScan}
                onError={handleScanError}
                style={{ width: '100%', height: '100%' }}
                delay={300}
                facingMode={currentCamera}
              />
              <button
                onClick={() => setCurrentCamera(currentCamera === 'environment' ? 'user' : 'environment')}
                className="absolute top-2 right-2 p-2 bg-indigo-600 text-white rounded-full z-10"
                title={currentCamera === 'environment' ? 'Passer  la camra frontale' : 'Passer  la camra arrire'}
              >
                <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                  <path d="M2 6a2 2 0 012-2h6a2 2 0 012 2v2a2 2 0 01-2 2H4a2 2 0 01-2-2V6zM14 6a2 2 0 012-2h2a2 2 0 012 2v8a2 2 0 01-2 2h-2a2 2 0 01-2-2V6z" />
                </svg>
              </button>
            </div>
            {scanError && (
              <div className="text-red-500 text-sm mt-2">{scanError}</div>
            )}
            <button
              className="mt-4 bg-red-500 text-white rounded-md px-4 py-2 hover:bg-red-600 w-full"
              onClick={closeScanner}
            >
              {t('closeScanner')}
            </button>
          </div>
        </div>
      )}
      
      {renderStopwatch()}
    </div>
  );
}
